import { html, LitElement } from '@polymer/lit-element';
import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import GraphChooser from './Components/GraphChooser';
import GraphDisplayer from './Components/GraphDisplayer';
import * as serviceWorker from './serviceWorker';

/**
 * The LitElement class is an ultra-light wrapper for the W3C HTMLElement class.
 * It is published by the Polymer team, and provides helpers for templating, binding and lifecycle functions.
 * refer: https://github.com/Polymer/lit-element
 *
 * For info on 'slot' tag, refer to:
 *  https://developer.mozilla.org/en-US/docs/Web/HTML/Element/slot#Browser_compatibility
 *  On browsers that do not natively support 'slot' elements, the webcomponentjs module will polyfill this.
 *
 *  This approach means that W3C standards can be used in consuming apps to pull in component apps,
 *  without any framework lockin.  The aim is that shared webcomponents can use whichever framework
 *  becomes popular over time, without consumers needing to know anything more than the webcomponent api.
 *
 */


class Graph2Data extends LitElement {
    static get properties() {
        return {
            config: { type: Object },
            apiDriver: { type: Boolean },
            devMode: { type: Boolean }
        };
    }
    constructor() {
        super();

        this.raisePointAdded = this.raisePointAdded.bind(this);
        this.raiseSeriesDeleted = this.raiseSeriesDeleted.bind(this);
        this.raiseCurrentSeriesChanged = this.raiseCurrentSeriesChanged.bind(this);
        this.raiseSeriesAdded = this.raiseSeriesAdded.bind(this);
        this.raiseSeriesLabelChanged = this.raiseSeriesLabelChanged.bind(this);
        this.raisePointDeleted = this.raisePointDeleted.bind(this);
        this.raiseExtractionComplete = this.raiseExtractionComplete.bind(this);
    }
    raisePointAdded(detail) {
        this.dispatchEvent(new CustomEvent("pointAdded", {
            bubbles: true,
            cancelable: false,
            composed: true,
            detail
        }));
    }
    raiseSeriesDeleted(detail) {
        this.dispatchEvent(new CustomEvent("seriesDeleted", {
            bubbles: true,
            cancelable: false,
            composed: true,
            detail
        }));
    }
    raiseCurrentSeriesChanged(detail) {
        this.dispatchEvent(new CustomEvent("currentSeriesChanged", {
            bubbles: true,
            cancelable: false,
            composed: true,
            detail
        }));
    }
    raiseSeriesAdded(detail) {
        this.dispatchEvent(new CustomEvent("seriesAdded", {
            bubbles: true,
            cancelable: false,
            composed: true,
            detail
        }));
    }
    raiseSeriesLabelChanged(detail) {
        this.dispatchEvent(new CustomEvent("seriesLabelChanged", {
            bubbles: true,
            cancelable: false,
            composed: true,
            detail
        }));
    }
    raisePointDeleted(detail) {
        this.dispatchEvent(new CustomEvent("pointDeleted", {
            bubbles: true,
            cancelable: false,
            composed: true,
            detail
        }));
    }
    raiseExtractionComplete(detail) {
        this.dispatchEvent(new CustomEvent("extractionComplete", {
            bubbles: true,
            cancelable: false,
            composed: true,
            detail
        }));
    }
    render() {
        const config = this.getAttribute('config');
        const apiDriver = this.getAttribute('apiDriver') === 'true';
        const devMode = this.getAttribute('devMode') === 'true';

        ReactDOM.render(<App
            localStorage={localStorage}
            config={config}
            apiDriver={apiDriver}
            devMode={devMode}
            raisePointAdded={this.raisePointAdded}
            raiseSeriesDeleted={this.raiseSeriesDeleted}
            raiseCurrentSeriesChanged={this.raiseCurrentSeriesChanged}
            raiseSeriesAdded={this.raiseSeriesAdded}
            raiseSeriesLabelChanged={this.raiseSeriesLabelChanged}
            raisePointDeleted={this.raisePointDeleted}
            raiseExtractionComplete={this.raiseExtractionComplete}
        />, this);
        return html`
            <slot></slot>
        `;
    }

    // Use this instead if shadow dom is not wanted. Generally you do want shadow dom for encapsulation.
    // https://developer.mozilla.org/en-US/docs/Web/Web_Components/Using_shadow_DOM

    /*
    public createRenderRoot(){ return this; }

    public connectedCallback() {
        ReactDOM.render(<App/>, this);
    }
    */
}

