import React, { Component } from 'react';

import AxisDiagram from './AxisDiagram';
import StepType from '../StepType';

import { keyDownArrowKeyPressed } from '../Utils';

export default class AxisCalibration extends Component {
    constructor(props) {
        super(props);

        this.mouseLeftClicked = this.mouseLeftClicked.bind(this);
        this.keyDown = this.keyDown.bind(this);
    }

    nextStepDesc() {
        const { planStep } = this.props;
        if (planStep) {
            return `${planStep.desc}`;
        }
        else {
            return ''
        }
    }

    mouseLeftClicked(event) {
        const { scaledOffsetX, scaledOffsetY } = event.detail.pointerFocus;
        const { planStep, cloneSourceAxisPointToDest } = this.props;
        if (planStep.type !== StepType.AXIS_CALIBRATION_POINT) return;
        this.props.addAxisPoint({ ...planStep, point: { x: scaledOffsetX, y: scaledOffsetY } }, () => {
            this.props.setCalibrationPlanIndex(
                currentIndex => currentIndex + 1,
                nextPlanStep => {
                    if (nextPlanStep.cloneDest) {
                        cloneSourceAxisPointToDest();
                    }
                }
            );
        });
    }

    _doNudgeAxisPointBy(ev, boost) {
        const pointIndex = ev.target.getAttribute('data-index');
        if (typeof pointIndex !== 'string') {
            return;
        }

        const { nudgeInfo, nudgeAxisPointBy } = this.props;
        const nudge = { x: 0, y: 0 };

        switch (ev.code) {
            case 'ArrowUp':
                nudge.y -= nudgeInfo.y.value;
                break;
            case 'ArrowDown':
                nudge.y += nudgeInfo.y.value;
                break;
            case 'ArrowLeft':
                nudge.x -= nudgeInfo.x.value;
                break;
            case 'ArrowRight':
                nudge.x += nudgeInfo.x.value;
                break;
            default:
                break;
        }
        if (nudge.x || nudge.y) {
            ev.preventDefault();
            const booster = (boost ? 10 : 1);
            nudgeAxisPointBy(pointIndex, Object.keys(nudge).reduce((n, k) => ({ ...n, [k]: n[k] * booster }), nudge));
        }
    }

    keyDown(ev) {
        if (ev.ctrlKey && keyDownArrowKeyPressed(ev)) {
            this._doNudgeAxisPointBy(ev, ev.shiftKey);
        }
    }

    componentDidMount() {
        this.props.subscribe('mouseLeftClicked', this.mouseLeftClicked);
        this.props.subscribe('keydown', this.keyDown);
    }

    componentWillUnmount() {
        this.props.unsubscribe('mouseLeftClicked', this.mouseLeftClicked);
        this.props.unsubscribe('keydown', this.keyDown);
    }

    render() {
        const {
            axisType,
            axisPointCols,
            axisDiagramEditValues,
            addAxisPointValue,
            axisLogSettingChanged,
            axisLogPowerSettingChanged,
            planStep,
            axisConfig,
            zoomToAxisPoint,
            axisPointInverseCols,
            combinedOrigin,
            toggleCombinedOrigin,
            fullCalibrationPlan
        } = this.props;
        return (
            <div className={'axis-calibration'}>
                <AxisDiagram
                    axisType={axisType}
                    combinedOrigin={combinedOrigin}
                    toggleCombinedOrigin={toggleCombinedOrigin}
                    diagramSize={200}
                    showPoints={true}
                    editValues={axisDiagramEditValues}
                    fullCalibrationPlan={fullCalibrationPlan}
                    planStep={planStep}
                    axisPointCols={axisPointCols}
                    axisPointInverseCols={axisPointInverseCols}
                    addAxisPointValue={addAxisPointValue}
                    axisLogSettingChanged={axisLogSettingChanged}
                    axisLogPowerSettingChanged={axisLogPowerSettingChanged}
                    axisConfig={axisConfig}
                    zoomToAxisPoint={zoomToAxisPoint}
                />
                <div className='cta'>{this.nextStepDesc()}</div>
            </div>
        );
    }
}
