import React, { Component } from 'react';
import disableBrowserBackButton from 'disable-browser-back-navigation';

import './App.scss';
import ApiDriver from './Components/ApiDriver';
import MainUi from './Components/MainUi';

class App extends Component {
    componentDidMount() {
        disableBrowserBackButton();
    }
    render() {
        const { apiDriver, ...moreProps } = this.props;

        return (
            <div className="App">
                {apiDriver
                    ? <ApiDriver {...moreProps} />
                    : <MainUi {...moreProps} />
                }
            </div>
        );
    }
}

export default App;
