import React from 'react';

import Delete from '@material-ui/icons/DeleteOutline'
import Add from '@material-ui/icons/Add'
import Edit from '@material-ui/icons/Edit'
import Save from '@material-ui/icons/Save'
import Cancel from '@material-ui/icons/Cancel'

export default class SeriesSelector extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            adding: false,
            editing: false,
            seriesLabel: ''
        };
        this.currentSeriesChanged = this.currentSeriesChanged.bind(this);
        this.addSeriesClicked = this.addSeriesClicked.bind(this);
        this.editSeriesClicked = this.editSeriesClicked.bind(this);
        this.saveSeriesChanged = this.saveSeriesChanged.bind(this);
        this.cancelAddOrEditClicked = this.cancelAddOrEditClicked.bind(this);
        this.saveSeriesKeyPress = this.saveSeriesKeyPress.bind(this);
        this.removeSeriesClicked = this.removeSeriesClicked.bind(this);
        this.seriesLabelChanged = this.seriesLabelChanged.bind(this);
    }
    currentSeriesChanged(ev) {
        const currentSeriesId = ev.target.value;


        this.props.currentSeriesChanged(currentSeriesId);
    }
    _toggleAdding() {
        this.setState((state, props) => ({ adding: !state.adding }));
    }
    _toggleEditing() {
        this.setState((state, props) => ({ editing: !state.editing }));
    }
    addSeriesClicked(ev) {
        this._toggleAdding();
    }
    editSeriesClicked(ev) {
        const { currentSeries } = this.props;
        this.setState({ seriesLabel: currentSeries.label });
        this._toggleEditing();
    }
    _resetUiAfterAddOrEdit() {
        this.setState({ seriesLabel: '', adding: false, editing: false });
    }
    saveSeriesChanged(ev) {
        const { adding, editing } = this.state;
        const { currentSeries } = this.props;
        if (adding) {
            this.props.addSeries(this.state.seriesLabel);
        }
        else if (editing) {
            this.props.updateSeries(currentSeries.id, this.state.seriesLabel);
        }
        this._resetUiAfterAddOrEdit();
    }
    cancelAddOrEditClicked(ev) {
        this._resetUiAfterAddOrEdit();
    }
    saveSeriesKeyPress(ev) {
        if (ev.charCode === 13) {
            this.saveSeriesChanged();
        }
    }
    removeSeriesClicked(ev) {
        const { currentSeries } = this.props;

        this.props.removeSeries(currentSeries.id);
    }
    seriesLabelChanged(ev) {
        this.setState({
            seriesLabel: ev.target.value
        });
    }
    noSeriesHasLabel(label) {
        return this.props.series.every(s => s.label !== label);
    }
    noSeriesButOursHasLabel(ourSeries, label) {
        return this.props.series.every(s => s.label !== label || s === ourSeries);
    }
    render() {
        const {
            series,
            currentSeries,
            editMode,
            seriesSelectorLines
        } = this.props;
        const { adding, editing, seriesLabel } = this.state;

        const ifHaveSeries = !!series
            && series.length > 0
            && !!currentSeries;

        const ifNoSeriesPresent =
            !series
            || series.length === 0;

        const ifAdding = adding;
        const ifEditing = editing;
        const ifAddingOrEditing = adding || editing;
        const ifNotCurrentlyAdding = !adding;
        const ifNotCurrentlyAddingOrEditing = !adding && !editing;
        const ifHaveCurrentSeries = !!currentSeries;

        const ifMayCreateNewSeries = adding
            && !!seriesLabel
            && seriesLabel.length > 0
            && this.noSeriesHasLabel(seriesLabel);

        const ifMayRemoveSeries = currentSeries
            && !currentSeries.fixed;

        const ifMayEditSeriesLabel = !currentSeries.fixed;

        const ifMayUpdateSeriesLabel = editing
            && !currentSeries.fixed
            && !!seriesLabel
            && seriesLabel.length > 0
            && this.noSeriesButOursHasLabel(currentSeries, seriesLabel)

        return (
            <div className='series-selector-panel'>
                {editMode &&
                    <div className='button-bar'>
                        {ifNotCurrentlyAddingOrEditing
                            && <button className='add-button'
                                onClick={this.addSeriesClicked}
                                title='Add new series'
                            ><Add /></button>
                        }
                        {ifNotCurrentlyAddingOrEditing
                            && <button className='edit-button'
                                onClick={this.editSeriesClicked}
                                disabled={!ifMayEditSeriesLabel}
                                title='Edit series label'
                            ><Edit /></button>
                        }
                        {ifNotCurrentlyAddingOrEditing && ifHaveCurrentSeries
                            && <button
                                className='remove-button'
                                disabled={!ifMayRemoveSeries}
                                onClick={this.removeSeriesClicked}
                                title='Remove series'
                            ><Delete /></button>
                        }
                        {ifAddingOrEditing &&
                            <button className='save-button'
                                onClick={this.saveSeriesChanged}
                                disabled={!ifMayCreateNewSeries && !ifMayUpdateSeriesLabel}
                                title='Save label'
                            ><Save /></button>
                        }
                        {(ifAdding || ifEditing) &&
                            <button className='cancel-add-button'
                                onClick={this.cancelAddOrEditClicked}
                                title='Cancel changes'
                            ><Cancel /></button>
                        }
                    </div>
                }
                {ifNotCurrentlyAddingOrEditing && ifHaveSeries && <select
                    size={seriesSelectorLines}
                    className='series-selector single-line'
                    onChange={this.currentSeriesChanged}
                    value={currentSeries.id}
                >
                    {series.map(s =>
                        <option
                            key={s.id}
                            value={s.id}
                        >{s.label}</option>
                    )}
                </select>
                }

                {ifNotCurrentlyAdding && ifNoSeriesPresent
                    && <select className='no-series-selector single-line'>
                        <option>{'No series yet'}</option>
                    </select>
                }

                {(ifAdding || ifEditing)
                    && <input type='text'
                        autoFocus
                        onChange={this.seriesLabelChanged}
                        value={this.state.seriesLabel}
                        className='series-label-editor'
                        placeholder={ifAdding ? 'New series label' : `(${currentSeries.label})`}
                        onKeyPress={this.saveSeriesKeyPress}
                    />
                }
            </div>
        )
    }
}