import React from 'react';

import { roundToPrecision } from '../Utils';

export default class MatrixDisplay extends React.Component {
    render() {
        const { matrix, precision } = this.props;
        if (!matrix) {
            return <div className='matrix-display'></div>;
        }
        const rows = [[
            matrix.a, matrix.c, matrix.e
        ], [
            matrix.b, matrix.d, matrix.f
        ]];
        return (
            <div className='matrix-display'>
                {rows.map((r, ri) =>
                    <div className='matrix-row' key={ri}>{
                        r.map((i, ii) =>
                            <span className='matrix-element' key={ii} data-value={i}>{
                                roundToPrecision(i, precision)
                            }</span>
                        )
                    }</div>
                )}
            </div>
        )
    }
}