
import React from 'react';

const SeriesSelector = (props) =>
    <ul>
        {props.series.map(s =>
            <li key={s.id}>
                <label>
                    <input type='radio'
                        onChange={props.onSelectionChanged}
                        checked={s.id === props.selectedId}
                        value={s.id}
                    />
                    {s.label}</label>
            </li>
        )}
    </ul>

export default SeriesSelector;
