import React from 'react';
import ZoomControls from './ZoomControls';

export default class Magnifier extends React.Component {
    constructor(props) {
        super(props);

        this.imageCanvas = React.createRef();

        this.renderCounter = 1

        this.state = {};

        this.mouseMove = this.mouseMove.bind(this);
        this._grabContexts = this._grabContexts.bind(this);
        this._drawCross = this._drawCross.bind(this);
    }

    _magnifierSize = { width: this.props.width, height: this.props.height };
    _mid = { x: this._magnifierSize.width / 2, y: this._magnifierSize.height / 2 };

    _grabContexts() {
        this.imageContext = this.imageCanvas.current.getContext('2d');
        this.imageChromeContext = this.imageContext;
        this.chromeContext = this.imageContext;
    }
    _drawCross() {
        const cc = this.chromeContext;

        cc.strokeStyle = 'rgba(0, 255, 255)';
        cc.globalCompositeOperation = 'difference';
        cc.lineWidth = 2;
        cc.beginPath();
        cc.moveTo(this._mid.x, 0);
        cc.lineTo(this._mid.x, this._magnifierSize.height);
        cc.moveTo(0, this._mid.y);
        cc.lineTo(this._magnifierSize.width, this._mid.y)
        cc.stroke();

        cc.globalCompositeOperation = 'source-over';
    }
    componentDidMount() {
        this._grabContexts();
        this._drawCross();

        this.props.subscribe('mousePointerMoved', this.mouseMove);
    }

    magnify(pointerFocus, drawCross = true) {
        if (pointerFocus) {
            this._pointerFocus = pointerFocus
        } else {
            return;
        }

        const { scaledOffsetX, scaledOffsetY, context, chromeContext, dpr } = pointerFocus;
        if (this.imageContext && this.imageChromeContext) {
            const w = this._magnifierSize.width, h = this._magnifierSize.height;

            const zoom = this.props.zoomInfo.value;
            const halfSource = {
                x: (w >> 1) / zoom,
                y: (h >> 1) / zoom
            };
            const x = scaledOffsetX - halfSource.x;
            const y = scaledOffsetY - halfSource.y;
            const ww = 2 * halfSource.x;
            const hh = 2 * halfSource.y;


            this.imageContext.clearRect(0, 0, w, h);
            this.imageContext.drawImage(
                context.canvas,
                x,
                y,
                ww, hh,
                0, 0,
                w, h
            );
            this.imageChromeContext.drawImage(
                chromeContext.canvas,
                x * dpr,
                y * dpr,
                ww * dpr, hh * dpr,
                0, 0,
                w, h
            );
            if (drawCross) {
                this._drawCross();
            }
        }
    }
    mouseMove(event) {
        const { pointerFocus } = event.detail;

        this.magnify(pointerFocus);

        // this.setState({ pointerFocus })
    }
    componentWillUnmount() {
        this.props.unsubscribe('mousePointerMoved', this.mouseMove);
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        const { refreshToggler } = this.props;
        if (prevProps.refreshToggler.toggle !== refreshToggler.toggle) {
            // if (!refreshToggler.point) return;
            this.magnify(refreshToggler.point
                ? {
                    ...this._pointerFocus,
                    scaledOffsetX: refreshToggler.point.x,
                    scaledOffsetY: refreshToggler.point.y
                } : this._pointerFocus,
                true //!refreshToggler.showCross
            );
        }
    }
    shouldComponentUpdate(nextProps, nextState) {
        return nextProps.zoomInfo !== this.props.zoomInfo
            || nextProps.refreshToggler !== this.props.refreshToggler;
    }
    render() {
        // console.log(`${this.renderCounter++}) Magnifier.render!`);
        const style = {
            ...this.props.stageBorderStyle,
            width: this._magnifierSize.width,
            height: this._magnifierSize.height
        };

        return (
            <div className={'magnifier-wrapper'}>
                <ZoomControls
                    zoomInfo={this.props.zoomInfo}
                    zoomOut={this.props.zoomOut}
                    zoomIn={this.props.zoomIn}
                    squaredChanged={this.props.squaredChanged}
                    label='swatch magnification'
                />
                <div className={'magnifier-stage'} style={style}>
                    <canvas
                        width={this._magnifierSize.width}
                        height={this._magnifierSize.height}
                        className={'image'}
                        ref={this.imageCanvas}
                    />
                    {/* <canvas
                        width={this._magnifierSize.width}
                        height={this._magnifierSize.height}
                        className={'image-chrome'}
                        ref={this.backCanvas}
                    /> */}

                    {/* <canvas
                        width={this._magnifierSize.width}
                        height={this._magnifierSize.height}
                        className={'image-chrome'}
                        ref={this.imageChromeCanvas}
                    />
                    <canvas
                        width={this._magnifierSize.width}
                        height={this._magnifierSize.height}
                        className={'chrome'}
                        ref={this.chromeCanvas}
                    /> */}
                </div>
            </div>
        );
    }
}
