
import AxisType from './AxisType';
import StepType from './StepType';
import { addMissingProperties } from './Utils';

const fullCalibrationPlan = combinedOrigin => [{
    type: StepType.AXIS_CALIBRATION_POINT,
    axis: AxisType.HORIZONTAL,
    pointIndex: 0,
    desc: combinedOrigin
        ? 'Choose origin'
        : 'Choose first horizontal axis point',
    cloneSource: combinedOrigin,
    point: { x: 'xmin', y: 'ymin' }
    // , stepIndex
}, {
    type: StepType.AXIS_CALIBRATION_POINT,
    axis: AxisType.HORIZONTAL,
    pointIndex: 1,
    desc: 'Choose second horizontal axis point',
    point: { x: 'xmax', y: 'ymin' }
    // , stepIndex
}, {
    type: StepType.AXIS_CALIBRATION_POINT,
    axis: AxisType.VERTICAL,
    pointIndex: 0,
    desc: combinedOrigin
        ? 'Origin already chosen'
        : 'Choose first vertical axis point',
    cloneDest: combinedOrigin,
    point: { x: 'xmin', y: 'ymin' }
    // , stepIndex
}, {
    type: StepType.AXIS_CALIBRATION_POINT,
    axis: AxisType.VERTICAL,
    pointIndex: 1,
    desc: 'Choose second vertical axis point',
    point: { x: 'xmin', y: 'ymax' }
    // , stepIndex
}, {
    type: StepType.AXIS_CALIBRATION_VALUES,
    may: [
        StepType.MAY_EXTRACT_AXES,
        StepType.MAY_BUILD_MATRIX
    ],
    desc: 'Enter axis values',
    // , stepIndex
}, {
    type: StepType.ENTER_DATA_POINTS,
    may: [
        StepType.MAY_EXTRACT_AXES,
        StepType.MAY_BUILD_MATRIX
    ],
    desc: 'Enter data points'
    // , stepIndex
}].map(plan => addMissingProperties(plan, { may: [], axis: null }));

export default fullCalibrationPlan;