import React from 'react';
import './style.scss';
import graphImageNames from './graphImageNames';

import { getImageAsDataUrl } from '../../../Utils';

export default class GraphChooserApiDriver extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            selectedGraphInfo: [],
            thumbSize: 150
        };
        this.graphChosenClicked = this.graphChosenClicked.bind(this);
        this.graphSelectionChanged = this.graphSelectionChanged.bind(this);
    }

    async getGraphImageData(graphInfo) {
        const data = await Promise.all(graphInfo
            .map(async gi => ({
                coords: gi.coords,
                dataUrl: await getImageAsDataUrl(this.indexToUrl(gi.number - 1))
            }))
        );
        return data;
    }
    async graphChosenClicked(ev) {
        const graphData = await this.getGraphImageData(this.state.selectedGraphInfo);

        const { raiseGraphChosen } = this.props;
        raiseGraphChosen({
            message: 'Graph chosen',
            graphData
        });
        this.setState({ selectedGraphInfo: [] });
    }
    graphSelectionChanged(ev) {
        const value = +ev.target.value;

        this.setState((state, props) => {
            const { thumbSize } = this.state;
            return {
                selectedGraphInfo: value === 0
                    ? []
                    : (
                        state.selectedGraphInfo.find(item => item.number === value)
                            ? state.selectedGraphInfo.filter(item => item.number !== value)
                            : [...state.selectedGraphInfo, {
                                number: value,
                                coords: {
                                    x: 0,
                                    y: (value - 1) * thumbSize,
                                    w: thumbSize,
                                    h: thumbSize
                                }
                            }]
                    )
            };
        });
    }
    indexToUrl(n) {
        return `http://localhost:5000/${graphImageNames[n]}`;
    }
    render() {
        const { selectedGraphInfo, thumbSize } = this.state;
        const selectedCount = selectedGraphInfo.length;

        return (
            <div className='graph-chooser-api-driver' title={'GraphChooser (ApiDriver)'}>
                <select
                    multiple={true}
                    size={2}
                    onChange={this.graphSelectionChanged}
                    value={selectedGraphInfo.number}>
                    {graphImageNames.map((name, i) => ({
                        name,
                        selected: selectedGraphInfo.find(item => item.number === i + 1),
                        url: this.indexToUrl(i),
                        number: i + 1,
                        nextSelected: selectedGraphInfo.find(item => item.number === i + 2)
                    })).map(ob =>
                        <option
                            key={ob.name}
                            value={ob.number}
                            style={{
                                background: `url('${ob.url}')`,
                                width: `${thumbSize}px`,
                                height: `${thumbSize}px`,
                                backgroundSize: 'cover',
                                border: `${ob.selected ? '2' : '0'}px solid blue`,
                                marginBottom: `${ob.nextSelected ? -2 : 0}px`
                            }}
                            title={ob.name}
                        ></option>
                    )}
                </select>

                {selectedCount > 0 &&
                    <button onClick={this.graphChosenClicked}>{`Choose ${selectedCount} Graph${selectedCount !== 1 ? 's' : ''}`}</button>
                }
            </div>
        )
    }
}