import React from 'react';

import GraphChooserApiDriver from './ApiDriver';
import GraphChooserMainUi from './MainUi';

export default class GraphChooser extends React.Component {
    render() {
        const { apiDriver, ...moreProps } = this.props;

        return (
            <div className="GraphChooser">
                {apiDriver
                    ? <GraphChooserApiDriver {...moreProps} />
                    : <GraphChooserMainUi {...moreProps} />
                }
            </div>
        )
    }
}