import React from 'react';
import elementResizeEvent, { unbind } from 'element-resize-event';

import { drawPoint, getOffset, getSide, getOppositeSide } from '../Utils';
import AxisType from '../AxisType';


class ImageDisplay extends React.Component {
    constructor() {
        super();
        this.state = {
            naturalWidth: -1,
            naturalHeight: -1,
            imageWidth: 'auto',
            imageHeight: 'auto',
            widthBound: null,
            dpr: window.devicePixelRatio
        };
        this.imageStage = React.createRef();
        this.imageCanvas = React.createRef();
        // this.chromeCanvas = React.createRef();
        this.chromeCanvas = this.imageCanvas;

        this._handleMouseMove = this._handleMouseMove.bind(this);
        this._handleClick = this._handleClick.bind(this);
        this.imageLoadCompletedShim = this.imageLoadCompletedShim.bind(this);
        this.imageLoadCompleted = this.imageLoadCompleted.bind(this);
        this.imageLoadErrored = this.imageLoadErrored.bind(this);
        this._setScale = this._setScale.bind(this);

        this.renderCounter = 1
    }

    imageLoadCompletedShim() {
        // console.log('imageLoadCompletedShim calling imageLoadCompleted!');
        this.imageLoadCompleted();
    }
    imageLoadCompleted() {
        const is = this.imageStage.current;
        if (!this.image) return;
        const { naturalWidth, naturalHeight } = this.image;

        const ratios = {
            width: is.offsetWidth / naturalWidth,
            height: is.offsetHeight / naturalHeight
        };
        const widthBound = ratios.width < ratios.height;

        const imageWidth = widthBound ? '100%' : 'auto';
        const imageHeight = widthBound ? 'auto' : '100%';

        this.setState({
            naturalWidth,
            naturalHeight,
            imageWidth,
            imageHeight,
            image: this.image,
            widthBound,
            scale: 1
        }, () => {
            this._setScale(() => {
                this.imageContext.drawImage(
                    this.image,
                    0, 0,
                    this.imageContext.canvas.width, this.imageContext.canvas.height
                );
                this.drawPoints(this.chromeContext);
            });

            window.setTimeout(() => {
                this._setScale(() => {
                    this.imageContext.drawImage(
                        this.image,
                        0, 0,
                        this.imageContext.canvas.width, this.imageContext.canvas.height
                    );
                });
                this.drawPoints(this.chromeContext);
            }, 50);
        });
    }
    imageLoadErrored() {
        this.setState({
            imageNaturalWidth: -1,
            imageNaturalHeight: -1
        });
    }
    _setScale(callback) {
        const { dpr } = this.state;

        const scales = {
            x: this.imageCanvas.current.width / this.imageStage.current.clientWidth / dpr,
            y: this.imageCanvas.current.height / this.imageStage.current.clientHeight / dpr
        };
        this.setState({ scale: this.state.widthBound ? scales.x : scales.y }, callback);
    }
    updateOverlayPosition() {
        window.setTimeout(() => {
            const duxPanelBackdrop = document.getElementsByClassName('duxpanel-backdrop')[0];
            if (duxPanelBackdrop) {
                this.props.updateOverlay(duxPanelBackdrop);
            }
        }, 0);
    }
    componentDidMount() {

        this.imageContext = this.imageCanvas.current.getContext('2d');
        // this.chromeContext = this.chromeCanvas.current.getContext('2d');
        this.chromeContext = this.imageContext;

        const { dpr } = this.state;
        // this.imageContext.scale(dpr, dpr);
        this.chromeContext.scale(dpr, dpr);

        elementResizeEvent(this.imageStage.current, () => {
            // console.log('elementResizeEvent calling imageLoadCompletedShim!');
            this.imageLoadCompletedShim()
        });
        // console.log('componentDidMount calling imageLoadCompletedShim!');
        this.imageLoadCompletedShim();
        this.loadImage();

        this.chromeCanvas.current.addEventListener('mousemove', this._handleMouseMove);
        this.chromeCanvas.current.addEventListener('click', this._handleClick);
    }
    componentWillUnmount() {
        this.image.removeEventListener('load', this.imageLoadCompleted);
        this.image.removeEventListener('error', this.imageLoadErrored);

        this.chromeCanvas.current.removeEventListener('mousemove', this._handleMouseMove);
        this.chromeCanvas.current.removeEventListener('click', this._handleClick);

        unbind(this.imageStage.current);
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.axisConfig !== this.props.axisConfig) {
            this.drawPoints(this.chromeContext);
        }
        if (prevProps.currentSeries !== this.props.currentSeries
            || (prevProps.currentSeries
                && this.props.currentSeries
                && prevProps.currentSeries.points
                && this.props.currentSeries.points
                && prevProps.currentSeries.points.length !== this.props.currentSeries.points.length
            )
            || (prevProps.pointZoom !== this.props.pointZoom)
            || (prevProps.selectedSubPointId !== this.props.selectedSubPointId)
        ) {
            this.drawPoints(this.chromeContext);
        }
        if (prevProps.image !== this.props.image) {
            this.loadImage();
        }
        this.updateOverlayPosition();
    }
    loadImage() {
        if (this.image) {
            this.image.removeEventListener('load', this.imageLoadCompleted);
            this.image.removeEventListener('error', this.imageLoadErrored);
        }
        this.image = new window.Image();
        this.image.crossOrigin = 'Anonymous';
        this.image.addEventListener('load', this.imageLoadCompleted);
        this.image.addEventListener('error', this.imageLoadErrored);

        this.image.src = this.props.image;

        this.updateOverlayPosition();
    }
    _handleMouseMove(e) {
        var offset = getOffset(this.imageCanvas.current);

        const scrollX = this.imageStage.current.scrollLeft;
        const scrollY = this.imageStage.current.scrollTop;

        this.props.mousePointerMoved({
            x: e.clientX + scrollX,
            y: e.clientY + scrollY,
            offsetX: e.clientX - offset.x,
            offsetY: e.clientY - offset.y,
            context: this.imageContext,
            chromeContext: this.chromeContext,
            dpr: this.state.dpr,
            scale: this.state.scale
        });
    }
    _handleClick() {
        this.props.mouseLeftClicked();
    }
    shouldComponentUpdate(nextProps, nextState) {
        return nextProps.image !== this.props.image ||
            nextState.naturalWidth !== this.state.naturalWidth ||
            nextState.naturalHeight !== this.state.naturalHeight ||
            nextState.imageWidth !== this.state.imageWidth ||
            nextState.imageHeight !== this.state.imageHeight ||
            nextProps.axisConfig !== this.props.axisConfig ||
            nextProps.currentSeries !== this.props.currentSeries ||
            nextProps.pointZoom !== this.props.pointZoom ||
            nextProps.selectedSubPointId !== this.props.selectedSubPointId;
    }

    drawPoints(ctx) {
        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

        this.imageContext.drawImage(
            this.image,
            0, 0,
            this.imageContext.canvas.width, this.imageContext.canvas.height
        );

        const { dpr } = this.state;
        const {
            pointZoom,
            axisConfig,
            showCalibrationPoints,
            currentSeries,
            // selectedPointId,
            selectedSubPointId,
            selectedAxisPointId,
            dataType,
            axisPointInverseCols,
            axisType,
            combinedOrigin,
            calibrationPlan,
            showLabelPoints
        } = this.props;

        if (showCalibrationPoints) {
            const hiddenOriginPointIndex = combinedOrigin && (axisType === AxisType.BOTH) ? 2 : -1;
            const hiddenOriginPointId = hiddenOriginPointIndex !== -1 && axisConfig.points.length > hiddenOriginPointIndex
                ? axisConfig.points[hiddenOriginPointIndex].id
                : null;
            const nonHiddenOriginPointIndex = hiddenOriginPointIndex == 2 ? 0 : -1;

            axisConfig.points.forEach((point, i) => {
                if (i === hiddenOriginPointIndex) return;

                const selected =
                    point.id === selectedAxisPointId
                    || (
                        hiddenOriginPointId
                        && hiddenOriginPointId === selectedAxisPointId
                        && i === nonHiddenOriginPointIndex
                    );

                drawPoint({
                    ctx,
                    point,
                    selected,
                    dpr,
                    accentColor: axisPointInverseCols[i - (axisType === AxisType.VERTICAL ? -2 : 0)],
                    pointZoom
                });
            });
        }
        if (showLabelPoints) {
            const { labelConfig } = axisConfig;

            labelConfig.items.forEach(item => {
                const selected = labelConfig.selectedItemId === item.id;
                if (item.point) {
                    drawPoint({
                        ctx,
                        point: item.point,
                        selected,
                        dpr,
                        accentColor: axisPointInverseCols[0],
                        pointZoom
                    });
                }
            });
        }
        if (currentSeries) {
            const rpDp = dataType.dataPoints[0];
            // console.log(`currentSeries.points: ${currentSeries.points}`)
            currentSeries.points.forEach((point, i) => {
                const rp = point[rpDp.abbrev];
                Object.keys(point).forEach(pointName => {
                    if (pointName === 'id') return;

                    const subPoint = point[pointName];
                    const subPointDp = dataType.dataPoints[dataType.lookup[pointName]];
                    const side = getSide(axisType, subPoint, rp);

                    drawPoint({
                        ctx,
                        point: subPoint,
                        side,
                        selected: subPoint.id === selectedSubPointId,
                        dpr,
                        pointZoom
                    });
                    if (rp !== subPoint && subPointDp.symmetric) {
                        drawPoint({
                            ctx,
                            point: subPoint.mirrorPt,
                            side: getOppositeSide(side),
                            selected: false,
                            dpr,
                            pointZoom
                        });
                    }
                });
            });
        }
    }
    render() {
        const { naturalWidth, naturalHeight, dpr } = this.state;

        // console.log(`${this.renderCounter++}) ImageDisplay.render!`);
        return (
            <div className={'image-stage'} ref={this.imageStage}>
                <canvas
                    width={naturalWidth * dpr}
                    height={naturalHeight * dpr}
                    className={'image'}
                    ref={this.imageCanvas}
                    style={{ width: this.state.imageWidth, height: this.state.imageHeight }}
                />
                {/* <canvas
                    width={naturalWidth * dpr}
                    height={naturalHeight * dpr}
                    className={'chrome'}
                    ref={this.chromeCanvas}
                    style={{ width: this.state.imageWidth, height: this.state.imageHeight }}
                /> */}
            </div>
        );
    }
}

export default ImageDisplay;
