import React from 'react';
import { DuxDialog } from 'duxpanel';

import HelperDialogSteps from '../HelperDialogSteps';
import Wizard from './Wizard';
import WizardStep from './WizardStep';
import AxisDiagram from './AxisDiagram';
import AxisCalibration from './AxisCalibration';
import DataCollection from './DataCollection';
import LabelAxisCalibration from './LabelAxisCalibration';

import AxisType from '../AxisType';
import StepType from '../StepType';
import DiscriminatorType from '../DiscriminatorType';

import { capitalize } from '../Utils';

export default class HelperDialog extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dialogTitle: '',
            dialogModal: true
        }

        this.onTitleChange = this.onTitleChange.bind(this);
        this.haveAllCalibrationPoints = this.haveAllCalibrationPoints.bind(this);
        this.haveAllCalibrationPointsAndValues = this.haveAllCalibrationPointsAndValues.bind(this);
        this.setModal = this.setModal.bind(this);
    }
    onTitleChange(newTitle) {
        this.setState({ dialogTitle: newTitle });
    }
    plan = [{
        type: AxisType.HORIZONTAL,
        label: 'Horizontal axis'
    }, {
        type: AxisType.VERTICAL,
        label: 'Vertical axis'
    }, {
        type: AxisType.BOTH,
        label: 'Both axes'
    }]

    haveAllCalibrationPoints() {
        const { planStep } = this.props;
        const ret = !!planStep && planStep.type !== StepType.AXIS_CALIBRATION_POINT;
        return ret;
    }
    haveAllCalibrationPointsAndValues() {
        const { planStep } = this.props;
        const ret = !!planStep && planStep.type !== StepType.AXIS_CALIBRATION_POINT && this.props.haveAxisValues;
        return ret;
    }
    setModal(modal) {
        this.setState({ dialogModal: modal });
    }
    render() {
        const style = {
            left: '50%',
            right: '50%',
            transform: 'translate(-50%, -50%)'
        };
        const diagramSize = 100;
        const {
            config,
            showDiscriminator,
            selectedPointId,
            selectedSubPointId,
            currentPointIndex,
            currentDataPointIndex,
            selectPoint,
            nudgeInfo,
            nudgeSelectedPointBy,
            nudgeAxisPointBy,
            zoomToAxisPoint,
            moveCellSelection,
            cycleBetweenSeries,
            showRelativeValues,
            toggleShowRelativeValues,
            axisConfig,
            axisPointInverseCols,
            seriesSelectorLines,
            dataTableFontSize,
            labelConfig,
            addLabel,
            labelSelected,
            editMode,
            moveLabelSelection,
            setLabelPoint,
            zoomToLabelAxisPoint,
            nudgeLabelAxisPointBy,
            removeLabels
        } = this.props;

        const discriminatorStyle = {
            display: config.axisType === AxisType.BOTH
                ? 'inline-block'
                : 'none'
        }
        return (
            <DuxDialog
                show={this.props.show}
                width={'auto'}
                title={this.state.dialogTitle}
                onClose={this.onCloseDialog}
                style={style}
                modal={this.state.dialogModal}
            >
                <Wizard curStep={this.props.curStep}
                    prevClicked={this.props.prevClicked}
                    nextClicked={this.props.nextClicked}
                    finishClicked={this.props.finishClicked}
                    onTitleChange={this.onTitleChange}
                    setModal={this.setModal}
                >
                    <WizardStep title='Graph characteristics'
                        id={HelperDialogSteps.GraphCharacteristics}
                        cta='Choose the type of graph'
                    >
                        {this.props.dataTypes &&
                            (<select
                                size={this.props.dataTypes.length + 2}
                                onChange={this.props.graphTypeChanged}
                                value={config.graphTypeId}>
                                {this.props.dataTypes.map(gt =>
                                    <option
                                        key={gt.id}
                                        value={gt.id}
                                    >{gt.text}</option>
                                )}
                            </select>)}
                    </WizardStep>
                    {/* <WizardStep title='Series and points'
                        id={HelperDialogSteps.SeriesAndPoints}
                        cta='Enter initial numbers of series and points/series'
                    >
                        <div className='row'>
                            <div className='row'>
                                <label>
                                    # Data Series
                                <input
                                        type={'text'}
                                        name='data-series-count'
                                        value={config.dataSeriesCount}
                                        placeholder='auto'
                                        onChange={this.props.dataSeriesCountChanged} />
                                </label>
                            </div>
                            <div className={'row'}>
                                <label>
                                    # Data Points
                                <input
                                        type={'text'}
                                        name='data-points-count'
                                        value={config.dataPointsCount}
                                        placeholder='auto'
                                        onChange={this.props.dataPointsCountChanged} />
                                </label>
                            </div>
                        </div>
                    </WizardStep> */}
                    <WizardStep title='Calibration'
                        id={HelperDialogSteps.Calibration}
                        cta='Choose axes type'
                    >
                        <div className='row axis-diagram-container'>
                            {this.plan.map(({ type, label }) =>
                                <div className='axis-diagram' key={type}>
                                    <AxisDiagram
                                        axisType={type}
                                        diagramSize={diagramSize}
                                    />
                                    <label>
                                        <input
                                            type='radio'
                                            value={type}
                                            name='axisType'
                                            onChange={this.props.axisTypeChanged}
                                            checked={config.axisType === type}
                                        />
                                        {label}
                                    </label>
                                    {showDiscriminator && type === AxisType.BOTH
                                        && <div style={discriminatorStyle}>
                                            {Object.values(DiscriminatorType).map(dt =>
                                                <label key={dt}>
                                                    <input
                                                        type='radio'
                                                        value={dt}
                                                        name='discriminator'
                                                        onChange={this.props.discriminatorChanged}
                                                        checked={config.discriminator === dt}
                                                    />
                                                    {capitalize(dt)}
                                                </label>
                                            )}
                                        </div>
                                    }
                                </div>
                            )}
                        </div>
                        <div className={'row'}>
                            <label>
                                <input
                                    type='checkbox'
                                    name='includeIndividuals'
                                    checked={config.includeIndividuals}
                                    onChange={this.props.includeIndividualsChanged}
                                />
                                Include individuals
                            </label>
                        </div>
                    </WizardStep>
                    <WizardStep
                        title='Choose points'
                        id={HelperDialogSteps.ChoosePoints}
                        cta='Calibrate the axes'
                        nextOkIf={this.haveAllCalibrationPointsAndValues}
                        modal={false}
                    >
                        <AxisCalibration {...this.props.subscriptionHandlers}
                            combinedOrigin={this.props.combinedOrigin}
                            toggleCombinedOrigin={this.props.toggleCombinedOrigin}
                            addAxisPoint={this.props.addAxisPoint}
                            addAxisPointValue={this.props.addAxisPointValue}
                            axisLogSettingChanged={this.props.axisLogSettingChanged}
                            axisLogPowerSettingChanged={this.props.axisLogPowerSettingChanged}
                            removeAxisPoint={this.props.removeAxisPoint}
                            axisType={this.props.axisType}
                            setAxes={this.props.setAxes}
                            fullCalibrationPlan={this.props.fullCalibrationPlan}
                            setCalibrationPlanIndex={this.props.setCalibrationPlanIndex}
                            planStep={this.props.planStep}
                            cloneSourceAxisPointToDest={this.props.cloneSourceAxisPointToDest}
                            pointCount={this.props.pointCount}
                            axisDiagramEditValues={this.haveAllCalibrationPoints()}
                            axisPointCols={this.props.axisPointCols}
                            axisPointInverseCols={axisPointInverseCols}
                            axisConfig={axisConfig}
                            nudgeInfo={nudgeInfo}
                            nudgeAxisPointBy={nudgeAxisPointBy}
                            zoomToAxisPoint={zoomToAxisPoint}
                        />
                    </WizardStep>
                    <WizardStep title='Enter labels'
                        id={HelperDialogSteps.LabelAxisCalibration}
                        modal={false}>
                        <LabelAxisCalibration
                            {...this.props.subscriptionHandlers}
                            fontSize={dataTableFontSize.value}
                            labelConfig={labelConfig}
                            addLabel={addLabel}
                            labelSelected={labelSelected}
                            editMode={editMode}
                            moveLabelSelection={moveLabelSelection}
                            setLabelPoint={setLabelPoint}
                            zoomToLabelAxisPoint={zoomToLabelAxisPoint}
                            nudgeInfo={nudgeInfo}
                            nudgeLabelAxisPointBy={nudgeLabelAxisPointBy}
                            removeLabels={removeLabels}
                        />
                    </WizardStep>
                    <WizardStep
                        title='Data Collection'
                        id={HelperDialogSteps.DataCollection}
                        cta='Add data points'
                        modal={false}
                    >
                        <DataCollection {...this.props.subscriptionHandlers}
                            addDataPoint={this.props.addDataPoint}
                            removePoints={this.props.removePoints}
                            series={this.props.series}
                            currentSeries={this.props.currentSeries}
                            selectedPointId={selectedPointId}
                            selectedSubPointId={selectedSubPointId}
                            currentPointIndex={currentPointIndex}
                            currentDataPointIndex={currentDataPointIndex}
                            axisType={this.props.axisType}
                            setCalibrationPlanIndexByType={this.props.setCalibrationPlanIndexByType}
                            dataType={this.props.dataType}
                            currentSeriesChanged={this.props.currentSeriesChanged}
                            addSeries={this.props.addSeries}
                            updateSeries={this.props.updateSeries}
                            removeSeries={this.props.removeSeries}
                            editMode={editMode}
                            selectPoint={selectPoint}
                            nudgeInfo={nudgeInfo}
                            nudgeSelectedPointBy={nudgeSelectedPointBy}
                            moveCellSelection={moveCellSelection}
                            cycleBetweenSeries={cycleBetweenSeries}
                            showRelativeValues={showRelativeValues}
                            toggleShowRelativeValues={toggleShowRelativeValues}
                            seriesSelectorLines={seriesSelectorLines.value}
                            fontSize={dataTableFontSize.value}
                        />
                    </WizardStep>
                </Wizard>
            </DuxDialog >
        );
    }
}
