
import React from 'react';

const PointSelector = (props) =>
    <ul>
        {props.points.map((p, i) =>
            <li key={i}>
                <label>
                    <input type='radio'
                        onChange={props.onSelectionChanged}
                        checked={i === props.selectedIndex}
                        value={i}
                    />
                    {`(${p.x}, ${p.y})`}</label>
            </li>
        )}
    </ul>

export default PointSelector;
