import React from 'react';
import uuid from 'uuid/v4';

import SeriesSelector from './ApiDriverComponents/SeriesSelector';
import PointSelector from './ApiDriverComponents/PointSelector';

// UI to drive API investigation
class ApiDriver extends React.Component {
    constructor(props) {
        super(props);

        this.newPointXChange = this.newPointXChange.bind(this);
        this.newPointYChange = this.newPointYChange.bind(this);
        this.addPoint = this.addPoint.bind(this);
        this.deletePoint = this.deletePoint.bind(this);
        this.addSeries = this.addSeries.bind(this);
        this.deleteSeries = this.deleteSeries.bind(this);
        this.changeSeriesLabel = this.changeSeriesLabel.bind(this);
        this.extractionComplete = this.extractionComplete.bind(this);
        this.selectCurrentSeries = this.selectCurrentSeries.bind(this);
        this.selectCurrentPoint = this.selectCurrentPoint.bind(this);
        this.newSeriesLabelChange = this.newSeriesLabelChange.bind(this);
        this.currentSeriesLabelChange = this.currentSeriesLabelChange.bind(this);

        this.state = {
            series: [],
            metaData: {
                currentSeriesId: '',
                newPoint: { x: '', y: '' },
                addPointOk: false,
                newSeriesLabel: '',
                newSeriesLabelOk: false,
                updatedSeriesLabel: '',
                updatedSeriesLabelOk: false,
                currentPointIndex: -1,
                deleteCurrentPointOk: false
            }
        };
    }
    mayUpdateSeriesLabel(currentSeriesId, label) {
        const labelUsed = !!this.state.series.find(s => s.label === label);
        const labelOk = !!label;

        return !!currentSeriesId && labelOk && !labelUsed;
    }
    selectCurrentSeries(ev) {
        const currentSeriesId = ev.target.value;
        const currentSeries = this.state.series.find(s => s.id === currentSeriesId);
        this.setState({
            ...this.state,
            metaData: {
                ...this.state.metaData,
                currentSeriesId,
                addPointOk: this.mayAddPoint(currentSeriesId, this.state.metaData.newPoint),
                updatedSeriesLabel: currentSeries.label,
                updatedSeriesLabelOk: this.mayUpdateSeriesLabel(currentSeriesId, currentSeries.label)
            }
        });
    }
    selectCurrentPoint(ev) {
        const pointIndex = ev.target.value;
        this.setState({
            ...this.state,
            metaData: {
                ...this.state.metaData,
                currentPointIndex: +pointIndex,
                deleteCurrentPointOk: !!pointIndex
            }
        });
    }
    isValidPoint(pt) {
        const isValid = !isNaN(pt.x) && !isNaN(pt.y);

        return isValid;
    }
    mayAddPoint(currentSeriesId, pt) {
        return this.isValidPoint(pt) && !!currentSeriesId;
    }
    _doNewPointChange(ev, x) {
        const { newPoint } = this.state.metaData;
        const updatedNewPoint = { ...newPoint, [x ? 'x' : 'y']: ev.target.value };
        this.setState({
            ...this.state,
            metaData: {
                ...this.state.metaData,
                newPoint: updatedNewPoint,
                addPointOk: this.mayAddPoint(this.state.metaData.currentSeriesId, updatedNewPoint)
            }
        });
    }
    newPointXChange(ev) {
        this._doNewPointChange(ev, true);
    }
    newPointYChange(ev) {
        this._doNewPointChange(ev, false);
    }
    getCurrentSeries() {
        const { series, metaData } = this.state;

        return series.find(s => s.id === metaData.currentSeriesId);
    }
    addPoint(ev) {
        const { series, metaData } = this.state;
        const currentSeries = this.getCurrentSeries();
        const newPt = { ...metaData.newPoint };
        const newPoints = [...currentSeries.points, newPt];
        const newSeries = series.map(s => s.label !== currentSeries.label ? s : { ...s, points: newPoints });
        this.setState({
            ...this.state,
            series: newSeries,
            metaData: {
                ...this.state.metaData,
                newPoint: { x: '', y: '' }
            }
        }, () => {
            this.props.raisePointAdded({
                message: 'Point added to series',
                newPoint: newPt,
                updatedSeries: newSeries.filter(s => s.id === metaData.currentSeriesId)
            });
        });
    }
    deletePoint() {
        const { currentSeriesId, currentPointIndex } = this.state.metaData;
        const currentSeries = this.getCurrentSeries();
        const seriesBefore = { ...currentSeries };
        const deletionPoint = currentSeries.points[currentPointIndex];

        this.setState({
            ...this.state,
            series: this.state.series.map(s => s.id !== currentSeriesId ? s : {
                ...s,
                points: s.points.filter((p, i) => i !== currentPointIndex)
            }),
            metaData: {
                ...this.state.metaData,
                deleteCurrentPointOk: currentPointIndex < currentSeries.points.length - 1
            }
        }, () => {
            this.props.raisePointDeleted({
                message: 'Point deleted from series.',
                deletedPoint: deletionPoint,
                seriesBefore,
                seriesAfter: this.getCurrentSeries()
            });
        });
    }
    newSeriesLabelChange(ev) {
        const label = ev.target.value.trim();
        const labelUsed = !!this.state.series.find(s => s.label === label);
        const labelOk = label.length > 0;
        this.setState({
            ...this.state,
            metaData: {
                ...this.state.metaData,
                newSeriesLabel: label,
                newSeriesLabelOk: labelOk && !labelUsed
            }
        });
    }
    currentSeriesLabelChange(ev) {
        const label = ev.target.value.trim();
        this.setState({
            ...this.state,
            metaData: {
                ...this.state.metaData,
                updatedSeriesLabel: label,
                updatedSeriesLabelOk: this.mayUpdateSeriesLabel(this.state.metaData.currentSeriesId, label)
            }
        });
    }
    addSeries() {
        const newSeries = {
            id: uuid(),
            label: this.state.metaData.newSeriesLabel,
            points: []
        };
        this.setState({
            ...this.state,
            series: [
                ...this.state.series,
                newSeries
            ],
            metaData: {
                ...this.state.metaData,
                newSeriesLabel: '',
                newSeriesLabelOk: false
            }
        }, () => {
            this.props.raiseSeriesAdded({
                message: 'Series added.',
                newSeries
            });
        })
    }
    deleteSeries() {
        const seriesToDelete = this.getCurrentSeries();
        this.setState({
            ...this.state,
            series: this.state.series.filter(s => s !== seriesToDelete),
            metaData: {
                ...this.state.metaData,
                currentSeriesId: ''
            }
        }, () => {
            this.props.raiseSeriesDeleted({
                message: 'Series deleted.',
                seriesDeleted: seriesToDelete,
                allSeries: this.state.series
            });
        });
    }
    changeSeriesLabel() {
        const { currentSeriesId, updatedSeriesLabel } = this.state.metaData;
        const currentSeries = this.getCurrentSeries();
        const seriesBefore = { ...currentSeries };
        const seriesAfter = { ...currentSeries, label: updatedSeriesLabel };
        this.setState({
            ...this.state,
            series: this.state.series.map(s => s.id !== currentSeriesId ? s : seriesAfter),
            metaData: {
                ...this.state.metaData,
                updatedSeriesLabelOk: false
            }
        }, () => {
            this.props.raiseSeriesLabelChanged({
                message: 'Series labelChanged.',
                seriesBefore,
                seriesAfter
            });
        });
    }
    extractionComplete() {
        this.props.raiseExtractionComplete({
            message: 'Extraction complete.',
            series: this.state.series
        });
    }
    componentDidMount() {
        document.title = 'ApiDriver';
    }
    render() {
        const stateText = JSON.stringify(this.state, null, 2);
        const { metaData } = this.state;
        const currentSeries = this.getCurrentSeries();
        return (
            <div className='api-driver'>
                {/*<img src={this.props.fileUri} alt="graph" /><br />*/}
                <pre className='state-display'>
                    {stateText}
                </pre>
                <div className='state-editors'>
                    <fieldset className='select-series'>
                        <legend>Selected series</legend>
                        <SeriesSelector series={this.state.series}
                            onSelectionChanged={this.selectCurrentSeries}
                            selectedId={metaData.currentSeriesId}
                        />
                    </fieldset>
                    <fieldset className='add-point'>
                        <legend>Add point to selected series</legend>
                        <label>
                            x:
                            <input type='text'
                                className='coord'
                                value={metaData.newPoint.x}
                                onChange={this.newPointXChange}
                            />
                        </label>
                        <label>
                            y:
                            <input type='text'
                                className='coord'
                                value={metaData.newPoint.y}
                                onChange={this.newPointYChange}
                            />
                        </label>
                        <button
                            onClick={this.addPoint}
                            disabled={!metaData.addPointOk}>Add point</button><br />
                    </fieldset>
                    <fieldset className='delete-point'>
                        <legend>Delete point from selected series</legend>
                        <PointSelector
                            points={currentSeries ? currentSeries.points : []}
                            selectedIndex={metaData.currentPointIndex}
                            onSelectionChanged={this.selectCurrentPoint}
                        />
                        <button
                            onClick={this.deletePoint}
                            disabled={!metaData.deleteCurrentPointOk}
                        >Delete point</button><br />
                    </fieldset>
                    <fieldset className='add-series'>
                        <legend>Add series</legend>
                        <label>
                            Label:
                            <input type='text'
                                className='name'
                                value={metaData.newSeriesLabel}
                                onChange={this.newSeriesLabelChange}
                            />
                        </label>
                        <button
                            onClick={this.addSeries}
                            disabled={!metaData.newSeriesLabelOk}
                        >Add series</button><br />
                    </fieldset>
                    <fieldset className='delete-series'>
                        <legend>Delete series</legend>
                        <button disabled={!metaData.currentSeriesId} onClick={this.deleteSeries}>Delete series</button><br />
                    </fieldset>
                    <fieldset className='change-series'>
                        <legend>Change selected series label</legend>
                        <label>
                            Label:
                            <input type='text'
                                className='name'
                                value={metaData.updatedSeriesLabel}
                                onChange={this.currentSeriesLabelChange}
                            />
                        </label>
                        <button
                            onClick={this.changeSeriesLabel}
                            disabled={!metaData.updatedSeriesLabelOk}
                        >Change series label</button><br />
                    </fieldset>
                    <fieldset className='extraction-complete'>
                        <legend>Extraction complete</legend>
                        <button onClick={this.extractionComplete}>Extraction complete</button><br />
                    </fieldset>
                </div>
            </div>
        );
    }
}

export default ApiDriver;
