import React from 'react';
import Edit from '@material-ui/icons/Edit';
import More from '@material-ui/icons/More';
import ZoomTracksNudges from '@material-ui/icons/FindReplace';
import classNames from 'classnames';

import ZoomControls from './ZoomControls';

export default class Settings extends React.Component {
    constructor(props) {
        super(props);

        this.zoomTracksPointNudgesClicked = this.zoomTracksPointNudgesClicked.bind(this);
        this.editModeClicked = this.editModeClicked.bind(this);
        this.advancedModeClicked = this.advancedModeClicked.bind(this);
    }
    zoomTracksPointNudgesClicked(ev) {
        this.props.toggleZoomTracksPointNudgesClicked();
    }
    editModeClicked(ev) {
        this.props.toggleEditMode();
    }
    advancedModeClicked(ev) {
        this.props.toggleAdvancedMode();
    }
    render() {
        const {
            editMode,
            advancedMode,

            zoomInfo,
            zoomOut,
            zoomIn,
            pointZoomSquaredChanged,

            nudgeInfoX,
            nudgeZoomOutX,
            nudgeZoomInX,
            nudgeSquaredChangedX,

            nudgeInfoY,
            nudgeZoomOutY,
            nudgeZoomInY,
            nudgeSquaredChangedY,

            dataTableFontSizeInfo,
            dataTableFontSizeDown,
            dataTableFontSizeUp,

            zoomTracksPointNudges,
            
            seriesSelectorLines,
            seriesSelectorLinesUp,
            seriesSelectorLinesDown

        } = this.props;

        const zoomTracksNudgesTitle = `Magnifier tracks point movement: ${zoomTracksPointNudges ? 'ON' : 'OFF'}`;
        const editModeTitle = `edit mode ${editMode ? 'ON' : 'OFF'}`;
        const advancedModeTitle = `advanced mode ${advancedMode ? 'ON' : 'OFF'}`;

        return (
            <div className='settings'>
                <div title={zoomTracksNudgesTitle}
                    className={classNames({ 'toggle-icon': true, on: zoomTracksPointNudges })}>
                    <label>
                        <ZoomTracksNudges onClick={this.zoomTracksPointNudgesClicked} />
                    </label>
                </div>

                {advancedMode && <ZoomControls
                    showSquared={false}
                    zoomInfo={dataTableFontSizeInfo}
                    zoomOut={dataTableFontSizeDown}
                    zoomIn={dataTableFontSizeUp}
                    label='data-table font size'
                    showMultiplier={false}
                />}
                {advancedMode && <ZoomControls
                    showSquared={false}
                    zoomInfo={seriesSelectorLines}
                    zoomOut={seriesSelectorLinesDown}
                    zoomIn={seriesSelectorLinesUp}
                    label='# series selector lines'
                    showMultiplier={false}
                />}
                {advancedMode && <ZoomControls
                    zoomInfo={nudgeInfoX}
                    zoomOut={nudgeZoomOutX}
                    zoomIn={nudgeZoomInX}
                    squaredChanged={nudgeSquaredChangedX}
                    label='x-nudge'
                    showMultiplier={false}
                />}
                {advancedMode && <ZoomControls
                    zoomInfo={nudgeInfoY}
                    zoomOut={nudgeZoomOutY}
                    zoomIn={nudgeZoomInY}
                    squaredChanged={nudgeSquaredChangedY}
                    label='y-nudge'
                    showMultiplier={false}
                />}
                {advancedMode && <ZoomControls
                    zoomInfo={zoomInfo}
                    zoomOut={zoomOut}
                    zoomIn={zoomIn}
                    squaredChanged={pointZoomSquaredChanged}
                    label='point zoom'
                />}
                <div title={advancedModeTitle}
                    className={classNames({ 'toggle-icon': true, on: advancedMode })}>
                    <label>
                        <More onClick={this.advancedModeClicked} />
                    </label>
                </div>
                <div title={editModeTitle}
                    className={classNames({ 'toggle-icon': true, on: editMode })}>
                    <label>
                        <Edit onClick={this.editModeClicked} />
                    </label>
                </div>
            </div>
        )
    }
}