import React from 'react';

import GraphDisplayerApiDriver from './ApiDriver';
import GraphDisplayerMainUi from './MainUi';

export default class GraphDisplayer extends React.Component {
    render() {
        const { apiDriver, ...moreProps } = this.props;

        return (
            <div className="GraphDisplayer">
                {apiDriver
                    ? <GraphDisplayerApiDriver {...moreProps} />
                    : <GraphDisplayerMainUi {...moreProps} />
                }
            </div>
        )
    }
}