import React from 'react';
import classNames from 'classnames';
import Delete from '@material-ui/icons/DeleteOutline';
import CheckBox from '@material-ui/icons/CheckBox';
import CheckBoxPartial from '@material-ui/icons/IndeterminateCheckBox';
import CheckBoxEmpty from '@material-ui/icons/CheckBoxOutlineBlank';

import SeriesSelector from './SeriesSelector';

import {
    roundToPrecision,
    getIntegerAttribute,
    keyDownArrowKeyPressed
} from '../Utils';
import AxisType from '../AxisType';
import StepType from '../StepType';

export default class DataCollection extends React.Component {
    constructor(props) {
        super(props)

        this.keyDown = this.keyDown.bind(this);

        this.props.setCalibrationPlanIndexByType(StepType.ENTER_DATA_POINTS);

        this.state = {
            selectedRows: []
        };

        this.mouseLeftClicked = this.mouseLeftClicked.bind(this);
        this.headClicked = this.headClicked.bind(this);
        this.bodyClicked = this.bodyClicked.bind(this);
        this.deleteClicked = this.deleteClicked.bind(this);
        this.toggleRowSelected = this.toggleRowSelected.bind(this);
        this.toggleAllRows = this.toggleAllRows.bind(this);
    }

    rowSelected(id) {
        return this.state.selectedRows.includes(id);
    }
    toggleRowSelected(ev) {
        const tr = ev.target.closest('tr');
        const id = tr.getAttribute('data-id');

        this.setState((state, props) => ({
            selectedRows: state.selectedRows.includes(id)
                ? state.selectedRows.filter(r => r !== id)
                : [...state.selectedRows, id]
        }));
    }
    toggleAllRows(ev) {
        const tbody = ev.target.closest('table').querySelector('tbody');
        const allRowsSelected = this.getAllRowsSelectedInfo();

        this.setState({
            selectedRows: allRowsSelected === 'all'
                ? []
                : [...tbody.children]
                    .map(r => r.getAttribute('data-id'))
                    .filter(r => r !== "")
        });
    }
    mouseLeftClicked(ev) {
        const { scaledOffsetX, scaledOffsetY, valX, valY } = ev.detail.pointerFocus;

        this.props.addDataPoint({
            point: { x: scaledOffsetX, y: scaledOffsetY },
            dataPoint: { x: valX, y: valY }
        });
    }

    headClicked(ev) {
        const th = ev.target.tagName.toLowerCase() === 'th'
            ? ev.target
            : null;

        if (th) {
            this.props.toggleShowRelativeValues();
        }
    }
    bodyClicked(ev) {
        const td = ev.target.tagName.toLowerCase() === 'td'
            ? ev.target
            : null;

        if (td) {
            const subPointId = td.getAttribute('data-id');
            const dataPointIndex = getIntegerAttribute(td, 'data-data-point-index');
            const blankCell = td.classList.contains('blank-cell');

            const tr = td.parentNode.tagName.toLowerCase() === 'tr'
                ? td.parentNode
                : null;

            if (tr && !td.classList.contains('independent')) {
                const pointIndex = getIntegerAttribute(tr, 'data-point-index');
                const pointId = tr.getAttribute('data-id');
                this.props.selectPoint(pointId, subPointId, pointIndex, dataPointIndex, blankCell);
            }
        }
    }

    deleteClicked(ev) {
        this.props.removePoints(this.state.selectedRows, () => {
            this.setState({ selectedRows: [] });
        });
    }

    _doNudgeSelectedPointBy(ev, boost) {
        const { nudgeInfo, nudgeSelectedPointBy } = this.props;
        const nudge = { x: 0, y: 0 };

        switch (ev.code) {
            case 'ArrowUp':
                nudge.y -= nudgeInfo.y.value;
                break;
            case 'ArrowDown':
                nudge.y += nudgeInfo.y.value;
                break;
            case 'ArrowLeft':
                nudge.x -= nudgeInfo.x.value;
                break;
            case 'ArrowRight':
                nudge.x += nudgeInfo.x.value;
                break;
            default:
                break;
        }
        if (nudge.x || nudge.y) {
            ev.preventDefault();
            const booster = (boost ? 10 : 1);
            nudgeSelectedPointBy(Object.keys(nudge).reduce((n, k) => ({ ...n, [k]: n[k] * booster }), nudge));
        }
    }
    _doCycleBetweenSeries(ev) {
        const { cycleBetweenSeries } = this.props;
        let nudge = 0;

        switch (ev.code) {
            case 'ArrowUp':
                nudge -= 1;
                break;
            case 'ArrowDown':
                nudge += 1;
                break;
            case 'ArrowLeft':
                nudge -= 1;
                break;
            case 'ArrowRight':
                nudge += 1;
                break;
            default:
                break;
        }
        if (nudge) {
            ev.preventDefault();
            cycleBetweenSeries(nudge);
        }
    }
    _doMoveCellSelection(ev) {
        const { moveCellSelection } = this.props;
        const nudge = { x: 0, y: 0 };

        switch (ev.code) {
            case 'ArrowUp':
                nudge.y -= 1;
                break;
            case 'ArrowDown':
                nudge.y += 1;
                break;
            case 'ArrowLeft':
                nudge.x -= 1;
                break;
            case 'ArrowRight':
                nudge.x += 1;
                break;
            default:
                break;
        }
        if (nudge.x || nudge.y) {
            ev.preventDefault();
            moveCellSelection(nudge);
        }
    }
    keyDown(ev) {
        if (ev.target !== document.body || !keyDownArrowKeyPressed(ev)) {
            return;
        }
        // console.log(`${ev.code}${ev.shiftKey ? '+Shift' : ''}${ev.altKey ? '+Alt' : ''}${ev.ctrlKey ? '+Ctrl' : ''}`)
        if (ev.ctrlKey) {
            this._doNudgeSelectedPointBy(ev, ev.shiftKey);
        }
        else if (ev.altKey) {
            this._doCycleBetweenSeries(ev);
        }
        else {
            this._doMoveCellSelection(ev);
        }
    }
    componentDidMount() {
        this.props.subscribe('mouseLeftClicked', this.mouseLeftClicked);
        this.props.subscribe('keydown', this.keyDown);
    }

    componentWillUnmount() {
        this.props.unsubscribe('mouseLeftClicked', this.mouseLeftClicked);
        this.props.unsubscribe('keydown', this.keyDown);
    }

    getAllRowsSelectedInfo() {
        const { points } = this.props.currentSeries;

        const pointCount = points.length;
        const selectedCount = this.state.selectedRows.length;

        return selectedCount === pointCount && pointCount > 0
            ? 'all'
            : selectedCount === 0
                ? 'none'
                : 'some';

    }
    render() {
        const {
            currentSeries,
            axisType,
            dataType,
            currentSeriesChanged,
            updateSeries,
            removeSeries,
            editMode,
            series,
            addSeries,
            showRelativeValues,
            toggleShowRelativeValues,
            seriesSelectorLines,
            fontSize
        } = this.props;

        const {
            currentPointIndex,
            currentDataPointIndex,
            points,
            autoAdd
        } = currentSeries;

        const pointCount = points.length;

        const { multiPoints } = dataType;
        const refPoint = dataType.dataPoints[0];

        const getPoint = (p, independent, showPlusMinus) => {
            const pm = showPlusMinus ? '±' : '';

            return axisType === AxisType.BOTH
                ? (
                    !multiPoints
                        ? (
                            // `(${roundToPrecision(p.x, 3)}, ${roundToPrecision(p.y, 3)})`
                            independent
                                ? `${roundToPrecision(p.x, 3)}`
                                : `${roundToPrecision(p.y, 3)}`

                        )
                        : (
                            independent
                                ? `${roundToPrecision(p.x, 3)}`
                                : `${pm}${roundToPrecision(p.y, 3)}`
                        )
                )
                : axisType === AxisType.HORIZONTAL
                    ? (
                        `${pm}${roundToPrecision(p.x, 3)}`
                    ) : (
                        `${pm}${roundToPrecision(p.y, 3)}`
                    );
        };

        const guardedGetPoint = (p, dp) => {
            const dp2 = dp.independent ? refPoint : dp;

            const p2 = p[dp2.abbrev];

            const isRelativePoint = !dp.isReferencePoint && !dp.independent && !dp.label;
            const dispPoint = !!p2
                ? (
                    isRelativePoint && !showRelativeValues
                        ? p2.dataPoint.origDp
                        : p2.dataPoint
                ) : null;

            if (dp2.label) {
                return p[refPoint.abbrev].label.label;
            }
            return !!p2 ? getPoint(
                dispPoint,
                dp.independent,
                isRelativePoint && showRelativeValues
            ) : '';
        };

        const dataPoints = axisType === AxisType.BOTH/*  && multiPoints */
            ? [
                {
                    name: 'Independent variable',
                    abbrev: 'x',
                    independent: true
                },
                ...dataType.dataPoints
            ]
            : axisType !== AxisType.BOTH
                ? [
                    {
                        name: 'Label',
                        abbrev: 'Label',
                        label: true
                    },
                    ...dataType.dataPoints
                ]
                : dataType.dataPoints;

        const getPointId = (p, dp) => {
            return !dp.independent && !dp.label && !!p[dp.abbrev]
                ? p[dp.abbrev].id
                : null;
        };

        const getClass = (p, dp, pointIndex) => {
            const isSelected = !dp.independent
                && pointIndex === currentPointIndex
                && dp.dataPointIndex === currentDataPointIndex;

            const id = getPointId(p, dp);
            return classNames({
                'selected': isSelected && !autoAdd,
                'data-cell': !dp.independent && !!id,
                'blank-cell': !dp.independent && !id,
                'independent': dp.independent,
                'label': dp.label,
                'notFromBlank': true,
                'auto-add': isSelected && autoAdd
            });
        };
        const getBlankClass = (dp, pointIndex) => {
            const isSelected = !dp.independent
                && pointIndex === currentPointIndex
                && dp.dataPointIndex === currentDataPointIndex;

            return classNames({
                'selected': isSelected && !autoAdd,
                'fromBlank': true,
                'independent': dp.independent,
                'blank-cell': !dp.independent,
                'auto-add': isSelected && autoAdd
            });
        };

        const allRowsSelected = this.getAllRowsSelectedInfo();
        const deletePointsButtonDisabled = this.state.selectedRows.length === 0;

        if (currentSeries) {
            return (
                <div>
                    <SeriesSelector
                        series={series}
                        currentSeries={currentSeries}
                        currentSeriesChanged={currentSeriesChanged}
                        addSeries={addSeries}
                        updateSeries={updateSeries}
                        removeSeries={removeSeries}
                        editMode={editMode}
                        seriesSelectorLines={seriesSelectorLines}
                    />
                    <div className='data-points-meta-table-container'>
                        <div className='button-bar'>
                            {editMode && <button
                                onClick={this.deleteClicked}
                                title='Delete selected points'
                                disabled={deletePointsButtonDisabled}
                            >
                                <Delete />
                            </button>}
                            {editMode &&
                                <button title='Show relative or absolute values'
                                    onClick={toggleShowRelativeValues}
                                >
                                    {showRelativeValues
                                        ? <CheckBox />
                                        : <CheckBoxEmpty />
                                    }
                                </button>
                            }
                        </div>
                    </div>
                    <div className='data-points-table-container'>
                        <table className='data-points-table' style={{ fontSize: `${fontSize}pt` }}>
                            <thead onClick={this.headClicked}><tr>
                                {editMode && <th key='select' title='Select all points'>
                                    {allRowsSelected === 'all'
                                        && <CheckBox onClick={this.toggleAllRows} />
                                    }
                                    {allRowsSelected === 'some'
                                        && <CheckBoxPartial onClick={this.toggleAllRows} />
                                    }
                                    {allRowsSelected === 'none'
                                        && <CheckBoxEmpty onClick={this.toggleAllRows} />
                                    }

                                </th>}
                                {dataPoints.map(dp =>
                                    <th title={`${dp.name} (click to toggle relative-mode)`}
                                        key={dp.abbrev}
                                    >{dp.abbrev}</th>
                                )}
                            </tr></thead>
                            <tbody onClick={this.bodyClicked}>
                                {points.map((p, i) =>
                                    <tr key={p.id}
                                        data-id={p.id}
                                        data-point-index={i}
                                    >
                                        {editMode && <td key='select'>
                                            {this.rowSelected(p.id)
                                                ? <CheckBox onClick={this.toggleRowSelected} />
                                                : <CheckBoxEmpty onClick={this.toggleRowSelected} />
                                            }
                                        </td>}
                                        {dataPoints.map(dp =>
                                            <td
                                                className={getClass(p, dp, i)}
                                                key={dp.abbrev}
                                                data-id={getPointId(p, dp)}
                                                data-data-point-index={dp.dataPointIndex}
                                            >{guardedGetPoint(p, dp)}</td>)}
                                    </tr>
                                )}
                                <tr key={null}
                                    data-id=''
                                    data-point-index={pointCount}
                                >
                                    {editMode && <td key='select'>
                                        <CheckBoxEmpty className='disabled' />
                                    </td>}

                                    {dataPoints.map((dp, dpi) =>
                                        <td
                                            className={getBlankClass(dp, pointCount)}
                                            key={dp.abbrev}
                                            data-id=''
                                            data-data-point-index={dp.dataPointIndex}
                                        >&nbsp;</td>)}
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            );
        }
        else {
            return (
                <div>
                    <SeriesSelector
                        series={series}
                        currentSeries={currentSeries}
                        currentSeriesChanged={currentSeriesChanged}
                        addSeries={addSeries}
                        updateSeries={updateSeries}
                        removeSeries={removeSeries}
                        editMode={editMode}
                    />
                </div>
            );
        }
    }
}