import { intersect } from 'line-intersect-2d';
import { scale, translate, compose, applyToPoint } from 'transformation-matrix';
import { pdfjs } from 'react-pdf';

import pointPlans from './PointPlans';
import AxisType from './AxisType';

export async function loadImageThen(srcUrl) {
    return new Promise((resolve, reject) => {
        const img = document.createElement('img');
        img.crossOrigin = 'Anonymous';
        img.onload = () => {
            resolve(img);
        };
        img.onerror = reject;
        img.src = srcUrl;
    });
}
export async function getImageAsDataUrl(srcUrl) {
    return await loadImageThen(srcUrl).then(async img => {
        const { naturalWidth, naturalHeight } = img;
        const canvas = document.createElement('canvas');
        canvas.width = naturalWidth;
        canvas.height = naturalHeight;
        const ctx = canvas.getContext('2d');
        ctx.drawImage(img, 0, 0, naturalWidth, naturalHeight);
        const dataUrl = ctx.canvas.toDataURL();
        return dataUrl;
    });
}
export function getImageDataAsDataUrl(canvas, x, y, width, height) {
    const ctx = canvas.getContext('2d');
    const imageData = ctx.getImageData(x, y, width, height);

    const canvas2 = document.createElement('canvas');
    canvas2.width = width;// * 2;
    canvas2.height = height;// * 2;
    // canvas2.scale = 1;
    const ctx2 = canvas2.getContext('2d');

    ctx2.putImageData(imageData, 0, 0);
    const dataUrl = ctx2.canvas.toDataURL();
    return dataUrl;
}
export function toRadians(deg) {
    return (Math.PI / 180) * deg;
}

// logPropChanges
// vetoList - object with keys of property names. If value is:
// false - exclude from logging
// true - include in logging
// If key is absent, include in logging
export function logPropChanges(prefix, prevProps, obj, vetoList = {}) {
    Object.entries(prevProps).forEach(([k, v]) => {
        if (v !== obj.props[k] && vetoList[k] !== false) {
            console.log(`${prefix}.${k}: ${JSON.stringify(v)} -> ${JSON.stringify(obj.props[k])}`);
        }
    });
}

export function getSide(axisType, subPoint, refPoint) {
    const side = !subPoint.dataPoint.origDp
        ? undefined
        : (
            (axisType === AxisType.VERTICAL || axisType === AxisType.BOTH)
                ? (
                    subPoint.dataPoint.origDp.y > refPoint.dataPoint.y
                        ? 'upper'
                        : 'lower'
                )
                : subPoint.dataPoint.origDp.x > refPoint.dataPoint.x
                    ? 'right'
                    : 'left'
        );

    return side;
}
export function getOppositeSide(side) {
    return {
        upper: 'lower',
        lower: 'upper',
        left: 'right',
        right: 'left'
    }[side];
}

export function drawPoint({ ctx, point, side, selected, dpr, accentColor, pointZoom }) {
    let pointType = point.type;
    if (typeof pointType === 'object' && side !== undefined) {
        pointType = pointType[side];
    }
    const plan = pointPlans[pointType];
    pointZoom = pointZoom || 1;
    const lineWidth = 1 * pointZoom;

    const px = point.x * dpr, py = point.y * dpr;
    ctx.lineWidth = lineWidth;
    plan.forEach(p => {
        // eslint-disable-next-line eqeqeq
        const drawRing = p.whenSelected === selected || p.whenSelected == undefined;
        if (drawRing) {
            ctx.beginPath();
            if (p.gco) {
                ctx.globalCompositeOperation = p.gco;
            }
            if (p.lineWidth) ctx.lineWidth = p.lineWidth * pointZoom;
            if (p.fillStyle) ctx.fillStyle = p.accent ? accentColor : p.fillStyle;
            if (p.strokeStyle) ctx.strokeStyle = p.accent ? accentColor : p.strokeStyle;
            ctx.setLineDash(p.lineDash ? p.lineDash : []);
            ctx.lineDashOffset = p.lineDashOffset ? p.lineDashOffset : 0;

            if (p.radius !== undefined) {
                ctx.arc(px, py, p.radius * dpr * pointZoom / 2, 0, Math.PI * 2, 0);
            }
            if (p.lines) {
                p.lines.forEach(c => {
                    if (c.moveTo) {
                        ctx.moveTo(pointZoom * c.moveTo.x + px, pointZoom * c.moveTo.y + py);
                    }
                    if (c.lineTo) {
                        ctx.lineTo(pointZoom * c.lineTo.x + px, pointZoom * c.lineTo.y + py);
                    }
                });
            }

            if (p.fillStyle) ctx.fill();
            if (p.strokeStyle) ctx.stroke();
            ctx.globalCompositeOperation = 'source-over';
        }
    });
}

// iterates the array in the order specified by the indices in 'order'
// callback is (elem, arrayIndex, iterationIndex) => {}
export function forEachOrder(ar, order, callback) {
    const ub = order.length - 1;
    for (var i = 0; i <= ub; i++) {
        const idx = order[i];
        callback(ar[idx], idx, i);
    }
}

// iterates the array in the 0..n order with the exception that index 'lastIndex' is skipped
// when encountered in its natural position and iterated after all others.
// callback is (elem, index) => {}
export function forEachExplicitLast(ar, lastIndex, callback) {
    const len = ar.length;
    const ub = len - 1;
    const order = [...Array(len)].map((_, i) => i < ub ? (i < lastIndex ? i : i + 1) : lastIndex);
    return forEachOrder(ar, order, callback);
}

export function getLineIntersection(pt1, pt2, pt3, pt4) {
    const tolerance = 0.000005; // default
    const allowExtend = true;
    const point = intersect([pt1.x, pt1.y], [pt2.x, pt2.y], [pt3.x, pt3.y], [pt4.x, pt4.y], tolerance, allowExtend);
    return point;
}

export function roundTo(num, decimalPlaces) {
    // eslint-disable-next-line eqeqeq
    const ret = num != undefined ? num.toFixed(decimalPlaces) : num;
    return ret;
}

export function roundToPrecision(num, totalDigits) {
    // eslint-disable-next-line eqeqeq
    const ret = num != undefined ? num.toPrecision(totalDigits) : num;
    return ret;
}

// origin: [x: number, y: number],
// scales: [x: number, y: number]
export function buildMatrix(origin, scales) {
    const tm = translate(...origin);
    const sm = scale(...scales);
    const matrix = compose(sm, tm);

    return matrix;
}

// matrix: [ a: number, b: number, c: number, d: number, e: number, f: number ]
// point: { x: number, y: number }
export function transformPoint(matrix, point) {
    const transformedPoint = applyToPoint(matrix, point);

    return transformedPoint;
}

export function addMissingProperties(destOb, newProps) {
    return Object.keys(newProps)
        .reduce((a, newProp) => !a.hasOwnProperty(newProp)
            ? { ...a, [newProp]: newProps[newProp] }
            : a,
            destOb
        );
}

export function tweakColour(col, amt) {

    var usePound = false;

    if (col[0] === '#') {
        col = col.slice(1);
        usePound = true;
    }

    var num = parseInt(col, 16);

    var r = (num >> 16) + amt;

    if (r > 255) r = 255;
    else if (r < 0) r = 0;

    var b = ((num >> 8) & 0x00FF) + amt;

    if (b > 255) b = 255;
    else if (b < 0) b = 0;

    var g = (num & 0x0000FF) + amt;

    if (g > 255) g = 255;
    else if (g < 0) g = 0;

    return (usePound ? '#' : '') + (g | (b << 8) | (r << 16)).toString(16);
}

export function capitalize(str) {
    str = str.trim();

    return str && str.length > 0
        ? str.charAt(0).toUpperCase() + (str.length > 1
            ? str.slice(1).toLowerCase()
            : ''
        )
        : '';
}

export function getIntegerAttribute(elem, attrName) {
    const attr = elem.getAttribute(attrName);
    const value = attr !== undefined ? +attr : attr;

    return value;
}

export function removeProps(ob, excludeProps = []) {
    return Object.entries(ob).filter(([k, _]) => !excludeProps.includes(k)).reduce((acc, [k, v]) => ({ ...acc, [k]: v }), {});
}

export function dumpState(component, comment, topLevelWhiteList) {
    const restrictedState = removeProps(
        component.state, [
        'config',
        'pointerFocus',
        ...Object.keys(component.state).filter(k => !!topLevelWhiteList && !topLevelWhiteList.includes(k))
    ]
    );
    console.log(`${JSON.stringify(restrictedState, null, 2)}-${comment}`);
}


export async function putOnClipboard(text) {
    return await navigator.clipboard.writeText(text);
}

export async function getFromClipboard(text) {
    return await navigator.clipboard.readText();
}

export function keyDownArrowKeyPressed(ev) {
    const keys = ['ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight']
    return keys.includes(ev.code);
}

export function getMidpointsOfAdjacentPairs(points) {
    const midpoints = points.reduce((ar, v, i) => {
        if (i % 2 === 0) {
            ar.push({ ...v });
        } else {
            const ob = ar[Math.floor(i / 2)];
            ob.x = (ob.x + v.x) / 2;
            ob.y = (ob.y + v.y) / 2;
        }
        return ar;
    }, []);

    return midpoints;
}

export function isEmptyObject(ob) {
    return Object.entries(ob).length === 0 && ob.constructor === Object;
}

export function applyIntegerLimits(val, min, max) {
    return Math.max(
        Math.min(val, max || Number.MAX_SAFE_INTEGER),
        min || Number.MIN_SAFE_INTEGER
    );
}

export function charRange(fromChar, toChar) {
    const from = fromChar.charCodeAt(0);
    const to = toChar.charCodeAt(0);
    return [...Array(to - from + 1).keys()].map(k => String.fromCharCode(from + k));
}

export function getOffset(el) {
    var x = 0;
    var y = 0;

    while (el && !isNaN(el.offsetLeft) && !isNaN(el.offsetTop)) {
        // FF & IE don't support body's scrollTop - use window instead
        x += el.offsetLeft - (el.tagName === 'BODY' ? window.pageXOffset : el.scrollLeft);
        y += el.offsetTop - (el.tagName === 'BODY' ? window.pageYOffset : el.scrollTop);
        el = el.offsetParent;
    }

    return { x: x, y: y };
}

export function readAsDataURL(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();

        reader.onload = () => resolve(reader.result);
        reader.onerror = (event) => {
            switch (event.target.error.code) {
                case event.target.error.NOT_FOUND_ERR:
                    return reject(new Error('Error while reading a file: File not found.'));
                case event.target.error.NOT_READABLE_ERR:
                    return reject(new Error('Error while reading a file: File not readable.'));
                case event.target.error.SECURITY_ERR:
                    return reject(new Error('Error while reading a file: Security error.'));
                case event.target.error.ABORT_ERR:
                    return reject(new Error('Error while reading a file: Aborted.'));
                default:
                    return reject(new Error('Error while reading a file.'));
            }
        };
        reader.readAsDataURL(file);

        return null;
    });
}

export function getSvgNodeInfo(node) {
    var svg = node.ownerSVGElement;

    let pt1 = svg.createSVGPoint();
    pt1.x = node.x.baseVal.value;
    pt1.y = node.y.baseVal.value;
    const width = node.width.baseVal.value;
    const height = node.height.baseVal.value;

    const transforms = [];
    while (true) {
        var transform = node.transform.baseVal.consolidate();
        if (transform) {
            const { matrix } = transform;
            transforms.push(matrix);
        }
        if (node.parentNode === svg)
            break;
        node = node.parentNode;
    }
    const cm = compose(...transforms.reverse());
    const combinedMatrix = svg.createSVGMatrix();
    Object.assign(combinedMatrix, cm);
    pt1 = pt1.matrixTransform(combinedMatrix);

    return {
        svg,
        x: pt1.x,
        y: pt1.y,
        width: width * combinedMatrix.a,
        height: height * combinedMatrix.d,
        combinedMatrix
    };
}

