import React, { useEffect, useState, useRef } from 'react';
import AxisType from '../AxisType';

import {
    isEmptyObject,
    roundToPrecision,
    json
} from '../Utils';

import MatrixDisplay from './MatrixDisplay';

const StatusDisplay = props => {
    const [state, setState] = useState({ mousePos: {}, valPos: {} });

    const propsRef = useRef();
    propsRef.current = props;

    useEffect(() => {
        const mouseMove = ev => {
            const { scaledOffsetX, scaledOffsetY, valX, valY, label } = ev.detail.pointerFocus;
            const { axisType, matrix } = propsRef.current;

            const labelText = !!label ? label.label : '-';
            const newState = {
                mousePos: {
                    x: roundToPrecision(scaledOffsetX, 3),
                    y: roundToPrecision(scaledOffsetY, 3),
                },
                valPos: {
                    x: axisType !== AxisType.VERTICAL ? roundToPrecision(valX, 3) : labelText,
                    y: axisType !== AxisType.HORIZONTAL ? roundToPrecision(valY, 3) : labelText
                }
            };
            setState(newState);
        };
        props.subscribe('mousePointerMoved', mouseMove);

        return () => {
            props.unsubscribe('mousePointerMoved', mouseMove);
        };
    }, []);

    const { mousePos, valPos } = state;
    return (
        <div className={'status-display'}>
            <div className={'header'}>Status</div>
            <div className='body'>
                <div>imageSpace: ({mousePos.x}, {mousePos.y})</div>
                {/* {matrix && !isEmptyObject(matrix) && <MatrixDisplay matrix={matrix} precision={3} />} */}
                <div>dataSpace: ({valPos.x}, {valPos.y})</div>
            </div>
        </div>
    );
}

export default StatusDisplay;
