import React from 'react';
import classNames from 'classnames';

import Link from '@material-ui/icons/Link';
import LinkOff from '@material-ui/icons/LinkOff';

import AxisType from '../AxisType';
import {
    drawPoint,
    forEachExplicitLast,
    getMidpointsOfAdjacentPairs,
    charRange,
    px,
    svgNs,
    svgMirrorTitles
} from '../Utils';
import PointType from '../PointType';
import StepType from '../StepType';

export default class AxisDiagram extends React.Component {
    constructor(props) {
        super(props);

        this.canvas = React.createRef();
        // this.debugPointRefs = [...Array(4).keys()].map(_ => React.createRef());
        this.linkRefs = [...Array(1).keys()].map(_ => React.createRef());
        this.editRefs = [...Array(4).keys()].map(_ => React.createRef());
        this.logRefs = [...Array(2).keys()].map(_ => React.createRef());
        this.logPowerRefs = [...Array(2).keys()].map(_ => React.createRef());
        this._ourRef = React.createRef();

        this.state = {
            dpr: window.devicePixelRatio,
            ctx: null
        };

        this._validAxisValues = [...charRange('0', '9'), '.', '-'].map(c => c.charCodeAt(0));

        this._buildLayout();
        this.draw = this.draw.bind(this);
        this.positionEdit = this.positionEdit.bind(this);
        this.axisCalibrationValueChanged = this.axisCalibrationValueChanged.bind(this);
        this.axisLogSettingChanged = this.axisLogSettingChanged.bind(this);
        this.axisLogPowerSettingChanged = this.axisLogPowerSettingChanged.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.onKeyPress = this.onKeyPress.bind(this);
    }

    _buildLayout() {
        const { dpr } = this.state;
        const { diagramSize, showPoints, editValues } = this.props;
        const
            canvasPadding = (showPoints ? 15 : 0) + (editValues ? 0 : 0),
            xTick = 2.5,
            yTick = 2.5,
            xTicks = 5,
            yTicks = 5,
            yBars = 5,
            xBars = 5,
            xmin = 0 + xTick + 1 + canvasPadding,
            ymin = diagramSize / 2 - 1 - yTick - canvasPadding,
            xmax = diagramSize / 2 - 1 - canvasPadding,
            ymax = 1 + canvasPadding,
            xminDom = xmin + canvasPadding,
            yminDom = diagramSize - 1 - yTick - canvasPadding,
            xmaxDom = diagramSize - 1 - canvasPadding * 2,
            ymaxDom = ymax + canvasPadding;

        this._layout = {
            xTick,
            yTick,
            xTicks,
            yTicks,
            yBars,
            xBars,
            xmin,
            ymin,
            xmax,
            ymax,
            xminDom,
            yminDom,
            xmaxDom,
            ymaxDom
        };
    }
    draw() {
        const { ctx } = this.state;
        ctx.strokeStyle = 'black';
        const self = this;

        const {
            xTick,
            yTick,
            xTicks,
            yTicks,
            yBars,
            xBars,
            xmin,
            ymin,
            xmax,
            ymax
        } = self._layout;

        const drawHorizAxisTicks = () => {
            ctx.setLineDash([]);
            const step = (xmax - xmin) / xTicks;
            for (let x = xmin; x <= xmax; x += step) {
                ctx.beginPath();
                ctx.moveTo(x, ymin - 0.5);
                ctx.lineTo(x, ymin + yTick);
                ctx.stroke();
            }
        };
        const drawVertAxisTicks = () => {
            ctx.setLineDash([]);
            const step = (ymax - ymin) / yTicks;
            for (let y = ymin; y >= ymax; y += step) {
                ctx.beginPath();
                ctx.moveTo(xmin + 0.5, y);
                ctx.lineTo(xmin - xTick, y);
                ctx.stroke();
            }
        };

        const drawHorizBars = () => {
            ctx.setLineDash([]);
            const step = (xmax - xmin) / xBars;
            const barWidth = step * 0.6;
            const stepMidWidth = step / 2;
            for (let x = xmin + stepMidWidth; x <= xmax; x += step) {
                ctx.beginPath();
                ctx.strokeStyle = 'magenta';
                ctx.lineWidth = barWidth;
                ctx.moveTo(x, ymin - 0.5);
                ctx.lineTo(x, ymin - Math.random() * (ymin - ymax));
                ctx.stroke();
            }
        };

        const drawVertBars = () => {
            ctx.setLineDash([]);
            const step = (ymax - ymin) / yBars;
            const barWidth = step * 0.6;
            const stepMidWidth = step / 2;
            for (let y = ymin + stepMidWidth; y >= ymax; y += step) {
                ctx.beginPath();
                ctx.strokeStyle = 'green';
                ctx.lineWidth = Math.abs(barWidth);
                ctx.moveTo(xmin + 0.5, y);
                ctx.lineTo(xmin + Math.random() * (xmax - xmin), y);
                ctx.stroke();
            }
        };

        const drawCurvyLine = () => {
            ctx.beginPath();
            ctx.strokeStyle = 'blue';
            ctx.lineWidth = 1;
            const xr = xmax - xmin;
            const yr = ymin - ymax;
            const dx = xr / 15;

            ctx.moveTo(xmin, ymin);

            for (let x = xmin + dx; x <= xmax; x += dx) {
                const y = ymin - Math.random() * yr;
                ctx.lineTo(x, y);
            }
            ctx.stroke();
        }

        const dashLength = 1, gapLength = 1;
        const drawHorizAxis = (dashed = false) => {
            ctx.beginPath();
            ctx.setLineDash(dashed ? [dashLength, gapLength] : []);
            ctx.lineWidth = 1;
            ctx.moveTo(xmin, ymin);
            ctx.lineTo(xmax, ymin);
            ctx.stroke();
        };
        const drawVertAxis = (dashed = false) => {
            ctx.beginPath();
            ctx.setLineDash(dashed ? [dashLength, gapLength] : []);
            ctx.lineWidth = 1;
            ctx.moveTo(xmin, ymin);
            ctx.lineTo(xmin, ymax)
            ctx.stroke();
        };
        const drawPoints = (lastIndex) => {
            const { fullCalibrationPlan, planStep } = self.props;
            if (!fullCalibrationPlan) return;

            const selector = { xmin, xmax, ymin, ymax };

            const stepsEqual = (one, two) => {
                return one.axis === two.axis
                    && one.pointIndex === two.pointIndex;
            }

            const idx = lastIndex || fullCalibrationPlan.findIndex(elem => stepsEqual(elem, planStep));
            forEachExplicitLast(fullCalibrationPlan, idx, (p, i) => {
                if (p.type === StepType.AXIS_CALIBRATION_POINT && (p.axis === self.props.axisType || self.props.axisType === AxisType.BOTH)) {
                    let point = { x: selector[p.point.x], y: selector[p.point.y] };

                    drawPoint({
                        ctx,
                        point: {
                            ...point,
                            type: PointType.AXIS_CALIBRATION_SAMPLE
                        },
                        selected: stepsEqual(p, planStep),
                        dpr: 1,
                        accentColor: this.props.axisPointCols[i]
                    });
                }
            });
        };

        ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

        ctx.fillStyle = 'white';
        ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height);

        switch (this.props.axisType) {
            case AxisType.HORIZONTAL:
                drawHorizAxis();
                drawHorizAxisTicks();
                drawVertAxis(true);
                drawVertBars();
                break;

            case AxisType.VERTICAL:
                drawHorizAxis(true);
                drawVertAxisTicks();
                drawVertAxis();
                drawHorizBars();
                break;

            case AxisType.BOTH:
                drawHorizAxis();
                drawVertAxis();
                drawHorizAxisTicks();
                drawVertAxisTicks();
                drawCurvyLine();
                break;
            default:
                break;
        }
        drawPoints();
    }

    axisCalibrationValueChanged(ev) {
        this.props.addAxisPointValue({
            value: ev.target.value,
            pointIndex: +ev.target.getAttribute('data-index')
        });
    }

    onKeyPress(ev) {
        if (!this._validAxisValues.includes(ev.charCode)) {
            ev.preventDefault();
        }
    }

    axisLogSettingChanged(ev) {
        this.props.axisLogSettingChanged({
            axisIndex: +ev.target.getAttribute('data-index'),
            enabled: ev.target.checked
        });
    }

    axisLogPowerSettingChanged(ev) {
        this.props.axisLogPowerSettingChanged({
            axisIndex: +ev.target.getAttribute('data-index'),
            enabled: ev.target.checked
        });
    }

    positionEdit() {
        const { editValues, axisType } = this.props;
        const self = this;

        const overrides = [{}, {}, {
            left: '0',
            marginTop: '-40px',
        }, {
            left: '0',
            marginTop: '-15px'
        }];
        const cbOverrides = [{}, { left: 0, marginTop: '-34px' }];

        const getDisplay = i => {

            return axisType === AxisType.BOTH
                ? 'block'
                : i > 1
                    ? axisType === AxisType.HORIZONTAL ? 'none' : 'block'
                    : axisType === AxisType.VERTICAL ? 'none' : 'block';
        }
        const getLogDisplay = i => {
            return axisType === AxisType.BOTH
                ? 'block'
                : i > 0
                    ? axisType === AxisType.HORIZONTAL ? 'none' : 'block'
                    : axisType === AxisType.VERTICAL ? 'none' : 'block';
        };

        const one = (this.props.fullCalibrationPlan || [])
            .filter(plan => plan.type === StepType.AXIS_CALIBRATION_POINT)
            .map(plan => {
                return {
                    x: self._layout[plan.point.x + 'Dom'] + this.canvas.current.offsetLeft,
                    y: self._layout[plan.point.y + 'Dom'] + this.canvas.current.offsetTop
                };
            });
        const two = getMidpointsOfAdjacentPairs(one);

        one.map((p, i) => ({
            display: editValues ? getDisplay(i) : 'none',
            border: `2px solid palevioletred`,
            position: 'absolute',
            width: '50px',
            left: px(p.x),
            top: px(p.y)
        })).map((s, i) => ({
            ...s, ...overrides[i]
        })).forEach((s, i) => Object.assign(this.editRefs[i].current.style, s));

        const linkPos = one.length >= 2 ? {
            x: one[0].x - 50,
            y: one[2].y
        } : {};

        // one.map((p, i) => ({
        //     display: 'block',
        //     position: 'absolute',
        //     width: '3px',
        //     height: '3px',
        //     border: `1px solid ${this.props.axisPointCols[i]}`,
        //     left: px(p.x),
        //     top: px(p.y)
        // })).forEach((s, i) => Object.assign(this.debugPointRefs[i].current.style, s));

        this.linkRefs.map((_, i) => ({
            display: 'block',
            position: 'absolute',
            left: px(linkPos.x),
            top: px(linkPos.y)
        })).forEach((s, i) => this.linkRefs[i].current && Object.assign(this.linkRefs[i].current.style, s));

        two.map((p, i) => ({
            display: editValues && getLogDisplay(i),
            position: 'absolute',
            left: `${p.x}px`,
            top: `${p.y}px`
        })).map((s, i) => ({
            ...s, ...cbOverrides[i]
        })).forEach((s, i) => Object.assign(this.logRefs[i].current.style, s));

        two.map((p, i) => ({
            display: editValues && getLogDisplay(i),
            position: 'absolute',
            left: `${p.x}px`,
            top: `${p.y + 20}px`
        })).map((s, i) => ({
            ...s, ...cbOverrides[i]
        })).forEach((s, i) => Object.assign(this.logPowerRefs[i].current.style, s));
    }
    onFocus(ev) {
        this.props.zoomToAxisPoint(ev.target.getAttribute('data-index'));
    }
    focusEdit() {
        const edit = this.editRefs.find(er => er && er.current && er.current.style.display === 'block');
        edit && edit.current && edit.current.focus();
    }
    mirrorSvgTitles() {
        svgMirrorTitles(this._ourRef.current.getElementsByTagNameNS(svgNs, 'svg'));
    }

    componentDidMount() {
        const { dpr } = this.state;
        const ctx = this.canvas.current.getContext('2d');
        this.setState({ ctx }, () => {
            ctx.scale(dpr * 2, dpr * 2);
            this.draw();
        });
        this.positionEdit();
        this.focusEdit();
        this.mirrorSvgTitles();
    }
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (
            prevProps.axisType !== this.props.axisType
        ) {
            this.draw();
        }
        this.positionEdit();
        if (prevProps.planStep !== this.props.planStep) {
            this.draw();
            this.focusEdit();
        }
        if (prevProps.combinedOrigin !== this.props.combinedOrigin) {
            this.mirrorSvgTitles();
        }
    }
    render() {
        const {
            diagramSize,
            showPoints,
            editValues,
            axisType,
            axisConfig,
            combinedOrigin,
            toggleCombinedOrigin
        } = this.props;
        const { dpr } = this.state;
        const divSizePx = `${diagramSize + ((showPoints ? 30 : 0) + (editValues ? 30 : 0))}px`;
        const canvasSizePx = `${diagramSize}px`;

        const divStyle = {
            width: divSizePx,
            height: divSizePx,
            margin: 'auto',
            display: 'flex',
            justifyContent: 'center',
            flexDirection: 'column',
            position: 'relative'
        };
        const canvasStyle = {
            width: canvasSizePx,
            height: canvasSizePx,
            margin: 'auto'
        };
        const { points = [], axes = [] } = axisConfig || {};
        const axisValues = [...Array(4).keys()].map(i => i < points.length ? points[i].value || '' : '');
        const axisLogs = [...Array(2).keys()].map(i => i < axes.length ? axes[i].log || false : false);
        const axisLogPowers = [...Array(2).keys()].map(i => i < axes.length ? axes[i].logPower || false : false);

        return (
            <div style={divStyle} ref={this._ourRef}>
                {toggleCombinedOrigin
                    && combinedOrigin
                    && <Link className='combined-origin on'
                        title={`Origin points locked together`}
                        ref={this.linkRefs[0]}
                        onClick={toggleCombinedOrigin}
                    />}
                {toggleCombinedOrigin
                    && !combinedOrigin
                    && <LinkOff className='combined-origin off'
                        title={`Origin points free to move independently`}
                        ref={this.linkRefs[0]}
                        onClick={toggleCombinedOrigin}
                    />}

                {this.editRefs.map((r, i) => {
                    const dataIndex = axisType === AxisType.BOTH ? i : i % 2;
                    const value = editValues
                        ? axisType === AxisType.VERTICAL
                            ? axisValues[i - 2]
                            : axisValues[i]
                        : '';
                    return (
                        <input
                            className='axis-calibration-value'
                            ref={r}
                            type='text'
                            value={value}
                            onFocus={this.onFocus}
                            onKeyPress={this.onKeyPress}
                            onChange={this.axisCalibrationValueChanged}
                            key={i}
                            data-index={dataIndex}
                        />
                    );
                })}
                {/* {this.debugPointRefs.map((r, i) => <div ref={r} />
                )} */}

                {this.logRefs.map((r, i) => {
                    const dataIndex = axisType === AxisType.VERTICAL ? i - 1 : i;
                    return (
                        <label
                            key={i}
                            ref={r}
                            className='axis-log-setting'
                            title={axisLogs[dataIndex] ? 'Axis has log scale' : 'Axis has linear scale'}
                        >
                            <input type='checkbox'
                                onChange={this.axisLogSettingChanged}
                                data-index={dataIndex}
                                checked={axisLogs[dataIndex]}
                            />
                            log<span className='base'>10</span>
                        </label>
                    );
                })}
                {this.logPowerRefs.map((r, i) => {
                    const dataIndex = axisType === AxisType.VERTICAL ? i - 1 : i;
                    const disabled = !axisLogs[dataIndex];

                    return (
                        <label
                            key={i}
                            ref={r}
                            className={classNames('axis-log-power-setting', { 'disabled': disabled })}
                            title={disabled ? '' : axisLogPowers[dataIndex] ? 'Enter powers' : 'Enter values'}
                        >
                            <input type='checkbox'
                                onChange={this.axisLogPowerSettingChanged}
                                data-index={dataIndex}
                                disabled={disabled}
                                checked={axisLogPowers[dataIndex]}
                            />
                            10<span className='pow'>n</span>
                        </label>
                    );
                })}
                <canvas ref={this.canvas}
                    className='diagram'
                    width={diagramSize * dpr}
                    height={diagramSize * dpr}
                    style={canvasStyle}
                />
            </div>
        )
    }
}