import React, { useState, useEffect, useRef } from 'react';
import classNames from 'classnames';
import Delete from '@material-ui/icons/DeleteOutline';
import CheckBox from '@material-ui/icons/CheckBox';
import CheckBoxPartial from '@material-ui/icons/IndeterminateCheckBox';
import CheckBoxEmpty from '@material-ui/icons/CheckBoxOutlineBlank';

import {
    keyDownArrowKeyPressed,
    pt,
    nbsp,
    roundTo,
    json
} from '../../Utils';
import './style.scss';

const LabelAxisCalibration = props => {
    const [state, setState] = useState({ editingLabel: false, editingPoint: false, editingId: null });
    const [label, setLabel] = useState('');
    const [point, setPoint] = useState(null);
    const [selectedRows, setSelectedRows] = useState([]);
    const [prevId, setPrevId] = useState(null);

    const labelEditRef = useRef();
    const propsRef = useRef();

    const {
        fontSize,
        addLabel,
        labelSelected,
        editMode,
        zoomToLabelAxisPoint,
        moveLabelSelection,
        nudgeLabelAxisPointBy,
        removeLabels
    } = props;

    // console.log(`LabelAxisCalibration - props: ${json(props)}`);

    // labelConfigRef.current = labelConfig;
    propsRef.current = props;

    const items = editMode
        ? [...propsRef.current.labelConfig.items, { label: '', id: null, point: null }]
        : propsRef.current.labelConfig.items;

    const findItem = id => items.find(lc => lc.id === id);

    const getId = elem => elem.closest('tr').getAttribute('data-id');

    const mouseLeftClicked = event => {
        const { scaledOffsetX, scaledOffsetY } = event.detail.pointerFocus;

        const id = propsRef.current.labelConfig.selectedItemId;

        props.setLabelPoint(id, { x: scaledOffsetX, y: scaledOffsetY });
    };

    const _moveLabelSelection = ev => {
        const nudge = { x: 0, y: 0 };
        const id = propsRef.current.labelConfig.selectedItemId;

        switch (ev.code) {
            case 'ArrowUp':
                nudge.y -= 1;
                break;
            case 'ArrowDown':
                nudge.y += 1;
                break;
            case 'ArrowLeft':
                nudge.x -= 1;
                break;
            case 'ArrowRight':
                nudge.x += 1;
                break;
            default:
                break;
        }
        if (nudge.x || nudge.y) {
            ev.preventDefault();
            moveLabelSelection(id, nudge);
        }
    };

    const _doNudgeSelectedPointBy = (ev, boost) => {
        const nudge = { x: 0, y: 0 };
        const { nudgeInfo } = propsRef.current;
        console.log(`LabelAxisCalibration - nudgeInfo: ${json(nudgeInfo)}`);
        switch (ev.code) {
            case 'ArrowUp':
                nudge.y -= nudgeInfo.y.value;
                break;
            case 'ArrowDown':
                nudge.y += nudgeInfo.y.value;
                break;
            case 'ArrowLeft':
                nudge.x -= nudgeInfo.x.value;
                break;
            case 'ArrowRight':
                nudge.x += nudgeInfo.x.value;
                break;
            default:
                break;
        }
        if (nudge.x || nudge.y) {
            ev.preventDefault();
            const booster = (boost ? 10 : 1);
            nudgeLabelAxisPointBy(Object.keys(nudge).reduce((n, k) => ({ ...n, [k]: n[k] * booster }), nudge));
        }
    };

    const keyDown = ev => {
        if (ev.target !== document.body || !keyDownArrowKeyPressed(ev)) {
            return;
        }

        if (ev.ctrlKey) {
            _doNudgeSelectedPointBy(ev, ev.shiftKey);
        }
        else if (ev.altKey) {
        }
        else {
            _moveLabelSelection(ev);
        }
    };
    const editKeyDown = ev => {
        if (ev.key === 'Escape') {
            cancelEdit();
        }
        if (ev.key === 'Enter') {
            cancelEdit();
            const id = getId(ev.target);
            const config = findItem(id);
            addLabel({ ...config, label: label });
            setState({ editingLabel: true, editingPoint: false, editingId: null });
        }
    };
    const labelChange = ev => {
        setLabel(ev.target.value);
    };

    const cancelEdit = () => {
        setState({ editingLabel: false, editingPoint: false, editingId: null });
        setLabel('');
    };

    const rowClicked = ev => {
        const id = getId(ev.target);

        zoomToLabelAxisPoint(id);

        const labelCell = ev.target.closest('.label-cell');
        const pointCell = ev.target.closest('.point-cell');

        if (labelCell) {
            if (editMode && (state.editingId !== id || !state.editingLabel)) {
                setState({ editingPoint: false, editingId: id, editingLabel: true });
                const config = findItem(id);
                if (config) {
                    setLabel(config.label);
                }
            }
        } else if (pointCell) {
            if (editMode && (state.editingId !== id || !state.editingPoint)) {
                setState({ editingPoint: true, editingId: id, editingLabel: false });
            }
        }

        labelSelected(id);
    };

    useEffect(() => {
        props.subscribe('mouseLeftClicked', mouseLeftClicked);
        props.subscribe('keydown', keyDown);

        return () => {
            props.unsubscribe('mouseLeftClicked', mouseLeftClicked);
            props.unsubscribe('keydown', keyDown);
        };
    }, []);

    useEffect(() => {
        if (state.editingLabel) {
            labelEditRef.current.select();
            labelEditRef.current.focus();
        }
    }, [state.editingLabel, state.editingId]);

    useEffect(() => cancelEdit(), [props.editMode]);

    const textStyle = { fontSize: pt(fontSize) };
    const getLabel = item => item.label
        ? item.label
        : nbsp;

    const getPoint = item => item.point
        ? `${roundTo(item.point.x, 1)}, ${roundTo(item.point.y, 1)}`
        : '';

    const rowSelected = id => {
        return selectedRows.includes(id);
    };

    const toggleRowSelected = ev => {
        const id = getId(ev.target);
        if (!id) return;

        if (prevId && ev.shiftKey) {
            const prevIndex = items.findIndex(item => item.id === prevId);
            const index = items.findIndex(item => item.id === id);

            const from = Math.min(prevIndex, index);
            const to = Math.max(prevIndex, index);
            const range = items
                .filter((item, i) => i >= from && i <= to)
                .map(item => item.id);

            const toOff = selectedRows.includes(id);

            setSelectedRows(
                toOff
                    ? selectedRows.filter(r => !range.includes(r))
                    : [...selectedRows.filter(r => !range.includes(r)), ...range]
            );
        } else {
            setSelectedRows(
                selectedRows.includes(id)
                    ? selectedRows.filter(r => r !== id)
                    : [...selectedRows, id]
            );
        }
        setPrevId(id);
    };

    const removeLabelsClicked = () => {
        removeLabels(selectedRows, setSelectedRows([]));
    };

    const getAllRowsSelectedInfo = () => {
        const itemCount = items.length - 1;
        const selectedCount = selectedRows.length;

        return selectedCount === itemCount && itemCount > 0
            ? 'all'
            : selectedCount === 0
                ? 'none'
                : 'some';
    };

    const toggleAllRows = ev => {
        const tbody = ev.target.closest('table').querySelector('tbody');
        const allRowsSelected = getAllRowsSelectedInfo();

        setSelectedRows(
            allRowsSelected === 'all'
                ? []
                : [...tbody.children]
                    .map(r => r.getAttribute('data-id'))
                    .filter(id => !!id)
        );
    };

    const allRowsSelected = getAllRowsSelectedInfo();
    const removeLabelsButtonDisabled = allRowsSelected === 'none';

    return <div>
        <table className='label-config-table' style={textStyle}><thead>
            <tr>
                {editMode && <th title='Select all labels'>
                    {allRowsSelected === 'all'
                        && <CheckBox onClick={toggleAllRows} />
                    }
                    {allRowsSelected === 'some'
                        && <CheckBoxPartial onClick={toggleAllRows} />
                    }
                    {allRowsSelected === 'none'
                        && <CheckBoxEmpty onClick={toggleAllRows} />
                    }
                </th>}
                <th>Label</th>
                <th>Point</th>
            </tr></thead><tbody>

                {items.map(item => {
                    const cbClass = classNames({ disabled: !item.id });
                    return (
                        <tr key={item.id}
                            className={classNames({
                                selected: item.id === propsRef.current.labelConfig.selectedItemId
                            })}
                            onClick={rowClicked}
                            data-id={item.id}
                        >
                            {editMode && <td key='select'>
                                {rowSelected(item.id)
                                    ? <CheckBox className={cbClass} onClick={toggleRowSelected} />
                                    : <CheckBoxEmpty className={cbClass} onClick={toggleRowSelected} />
                                }
                            </td>}
                            {state.editingLabel && state.editingId === item.id
                                ?
                                <td key='editCell'
                                    className='edit-cell'>
                                    <input type='text'
                                        value={label}
                                        style={textStyle}
                                        onKeyDown={editKeyDown}
                                        onChange={labelChange}
                                        ref={labelEditRef}
                                        className='edit'
                                        size={1}
                                    />
                                </td>
                                : <td key='labelCell'
                                    className='label-cell'>
                                    {getLabel(item)}
                                </td>
                            }
                            <td key='pointCell'
                                className={classNames('point-cell', {
                                    interpolated: item.point && item.point.interpolated
                                })}>{getPoint(item)} </td>
                        </tr>
                    );
                })}
            </tbody></table>
        <div className='button-bar'>
            {editMode && <button
                onClick={removeLabelsClicked}
                title='Delete selected labels'
                disabled={removeLabelsButtonDisabled}
                className='delete-button'
            >
                <Delete />
            </button>}
        </div>
    </div>;
}

export default LabelAxisCalibration;
